import React, { useState, useEffect } from 'react';
import {
  Box,
  Card,
  CardContent,
  Typography,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  TextField,
  Button,
  Chip,
  IconButton,
  MenuItem,
  Select,
  FormControl,
  InputLabel,
  Pagination,
  CircularProgress,
  Alert,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Grid,
} from '@mui/material';
import { Search, Refresh, Visibility, Send } from '@mui/icons-material';
import { apiService } from '../../services/api';

interface Chat {
  id: number;
  user_id: string;
  category: string;
  sub_category?: string;
  title: string;
  status: string;
  created_at: string;
}

interface Message {
  id: number;
  chat_id: number;
  sender_type: string;
  content: string;
  created_at: string;
}

const SupportChatsPage: React.FC = () => {
  const [chats, setChats] = useState<Chat[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [status, setStatus] = useState('');
  const [category, setCategory] = useState('');
  const [search, setSearch] = useState('');
  const [page, setPage] = useState(1);
  const [size] = useState(20);
  const [totalPages, setTotalPages] = useState(1);
  const [total, setTotal] = useState(0);
  const [chatDialog, setChatDialog] = useState<{ open: boolean; chatId: number | null }>({
    open: false,
    chatId: null,
  });
  const [messages, setMessages] = useState<Message[]>([]);
  const [messagesLoading, setMessagesLoading] = useState(false);
  const [newMessage, setNewMessage] = useState('');

  const fetchChats = async () => {
    setLoading(true);
    setError(null);
    try {
      const response = await apiService.getAllChats({
        status: status || undefined,
        category: category || undefined,
        search: search || undefined,
        page,
        size,
      });
      
      if (response.data) {
        const data = response.data as any;
        setChats(data.items || data.data || []);
        setTotalPages(data.total_pages || 1);
        setTotal(data.total || 0);
      }
    } catch (err: any) {
      setError(err.message || 'Failed to fetch chats');
    } finally {
      setLoading(false);
    }
  };

  const fetchMessages = async (chatId: number) => {
    setMessagesLoading(true);
    try {
      const response = await apiService.getChatMessages(chatId, { page: 1, size: 100 });
      
      if (response.data) {
        const data = response.data as any;
        setMessages(data.items || data.data || []);
      }
    } catch (err: any) {
      setError(err.message || 'Failed to fetch messages');
    } finally {
      setMessagesLoading(false);
    }
  };

  useEffect(() => {
    fetchChats();
  }, [page]);

  const handleSearch = () => {
    setPage(1);
    fetchChats();
  };

  const handleOpenChat = (chatId: number) => {
    setChatDialog({ open: true, chatId });
    fetchMessages(chatId);
  };

  const handleSendMessage = async () => {
    if (!chatDialog.chatId || !newMessage.trim()) return;
    
    try {
      await apiService.sendChatMessage(chatDialog.chatId, newMessage);
      setNewMessage('');
      fetchMessages(chatDialog.chatId);
    } catch (err: any) {
      setError(err.message || 'Failed to send message');
    }
  };

  const getStatusColor = (status: string) => {
    switch (status.toLowerCase()) {
      case 'open':
        return 'info';
      case 'pending':
        return 'warning';
      case 'resolved':
        return 'success';
      case 'closed':
        return 'default';
      default:
        return 'default';
    }
  };

  return (
    <Box>
      <Typography variant="h4" gutterBottom>
        Support Chats
      </Typography>

      <Card sx={{ mb: 3 }}>
        <CardContent>
          <Box sx={{ display: 'flex', gap: 2, flexWrap: 'wrap', alignItems: 'center' }}>
            <TextField
              label="Search"
              variant="outlined"
              size="small"
              value={search}
              onChange={(e) => setSearch(e.target.value)}
              onKeyPress={(e) => e.key === 'Enter' && handleSearch()}
              sx={{ flexGrow: 1, minWidth: 200 }}
            />
            <FormControl size="small" sx={{ minWidth: 150 }}>
              <InputLabel>Status</InputLabel>
              <Select
                value={status}
                label="Status"
                onChange={(e) => setStatus(e.target.value)}
              >
                <MenuItem value="">All</MenuItem>
                <MenuItem value="open">Open</MenuItem>
                <MenuItem value="pending">Pending</MenuItem>
                <MenuItem value="resolved">Resolved</MenuItem>
                <MenuItem value="closed">Closed</MenuItem>
              </Select>
            </FormControl>
            <TextField
              label="Category"
              variant="outlined"
              size="small"
              value={category}
              onChange={(e) => setCategory(e.target.value)}
              sx={{ minWidth: 150 }}
            />
            <Button
              variant="contained"
              startIcon={<Search />}
              onClick={handleSearch}
            >
              Search
            </Button>
            <IconButton onClick={fetchChats} color="primary">
              <Refresh />
            </IconButton>
          </Box>
        </CardContent>
      </Card>

      {error && (
        <Alert severity="error" sx={{ mb: 2 }} onClose={() => setError(null)}>
          {error}
        </Alert>
      )}

      <Card>
        <CardContent>
          <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
            <Typography variant="h6">
              Chats ({total})
            </Typography>
          </Box>

          {loading ? (
            <Box sx={{ display: 'flex', justifyContent: 'center', p: 4 }}>
              <CircularProgress />
            </Box>
          ) : (
            <>
              <TableContainer component={Paper} variant="outlined">
                <Table>
                  <TableHead>
                    <TableRow>
                      <TableCell>ID</TableCell>
                      <TableCell>User ID</TableCell>
                      <TableCell>Category</TableCell>
                      <TableCell>Title</TableCell>
                      <TableCell>Status</TableCell>
                      <TableCell>Created At</TableCell>
                      <TableCell>Actions</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {chats.length === 0 ? (
                      <TableRow>
                        <TableCell colSpan={7} align="center">
                          <Typography color="textSecondary">No chats found</Typography>
                        </TableCell>
                      </TableRow>
                    ) : (
                      chats.map((chat) => (
                        <TableRow key={chat.id} hover>
                          <TableCell>{chat.id}</TableCell>
                          <TableCell>{chat.user_id}</TableCell>
                          <TableCell>
                            <Chip label={chat.category} size="small" variant="outlined" />
                          </TableCell>
                          <TableCell>{chat.title}</TableCell>
                          <TableCell>
                            <Chip
                              label={chat.status}
                              color={getStatusColor(chat.status) as any}
                              size="small"
                            />
                          </TableCell>
                          <TableCell>
                            {new Date(chat.created_at).toLocaleString()}
                          </TableCell>
                          <TableCell>
                            <IconButton
                              size="small"
                              color="primary"
                              onClick={() => handleOpenChat(chat.id)}
                            >
                              <Visibility />
                            </IconButton>
                          </TableCell>
                        </TableRow>
                      ))
                    )}
                  </TableBody>
                </Table>
              </TableContainer>

              {totalPages > 1 && (
                <Box sx={{ display: 'flex', justifyContent: 'center', mt: 3 }}>
                  <Pagination
                    count={totalPages}
                    page={page}
                    onChange={(_, newPage) => setPage(newPage)}
                    color="primary"
                  />
                </Box>
              )}
            </>
          )}
        </CardContent>
      </Card>

      <Dialog
        open={chatDialog.open}
        onClose={() => setChatDialog({ open: false, chatId: null })}
        maxWidth="md"
        fullWidth
      >
        <DialogTitle>Chat Messages</DialogTitle>
        <DialogContent>
          {messagesLoading ? (
            <Box sx={{ display: 'flex', justifyContent: 'center', p: 4 }}>
              <CircularProgress />
            </Box>
          ) : (
            <Box sx={{ maxHeight: 400, overflowY: 'auto', mb: 2 }}>
              {messages.map((message) => (
                <Box
                  key={message.id}
                  sx={{
                    mb: 2,
                    p: 2,
                    borderRadius: 1,
                    bgcolor: message.sender_type === 'admin' ? 'primary.light' : 'grey.200',
                    ml: message.sender_type === 'admin' ? 'auto' : 0,
                    mr: message.sender_type === 'admin' ? 0 : 'auto',
                    maxWidth: '70%',
                  }}
                >
                  <Typography variant="body2" fontWeight="bold">
                    {message.sender_type === 'admin' ? 'Admin' : 'User'}
                  </Typography>
                  <Typography variant="body1">{message.content}</Typography>
                  <Typography variant="caption" color="textSecondary">
                    {new Date(message.created_at).toLocaleString()}
                  </Typography>
                </Box>
              ))}
            </Box>
          )}
          <Box sx={{ display: 'flex', gap: 1, mt: 2 }}>
            <TextField
              fullWidth
              size="small"
              placeholder="Type a message..."
              value={newMessage}
              onChange={(e) => setNewMessage(e.target.value)}
              onKeyPress={(e) => e.key === 'Enter' && handleSendMessage()}
            />
            <Button
              variant="contained"
              startIcon={<Send />}
              onClick={handleSendMessage}
              disabled={!newMessage.trim()}
            >
              Send
            </Button>
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setChatDialog({ open: false, chatId: null })}>Close</Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default SupportChatsPage;

