import { Grid, Paper, Typography, Box, Chip } from "@mui/material";
import { useEffect, useState } from "react";
import { authApi } from "../../auth/AuthContext";

interface StatCardProps {
  label: string;
  value: string | number;
  accent?: string;
}

const StatCard = ({ label, value, accent }: StatCardProps) => (
  <Paper
    sx={{
      p: 2.5,
      borderRadius: 3,
      bgcolor: "rgba(15,23,42,0.95)",
      border: "1px solid rgba(148,163,184,0.25)",
    }}
  >
    <Typography variant="body2" color="text.secondary">
      {label}
    </Typography>
    <Typography variant="h5" mt={1} fontWeight={700}>
      {value}
    </Typography>
    {accent && (
      <Chip
        size="small"
        label={accent}
        sx={{ mt: 1, bgcolor: "rgba(22,163,74,0.15)", color: "#4ade80" }}
      />
    )}
  </Paper>
);

export const OverviewPage = () => {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [supportStats, setSupportStats] = useState<any>(null);

  useEffect(() => {
    const load = async () => {
      setLoading(true);
      setError(null);
      try {
        // Using available admin stats endpoints
        const [supportRes] = await Promise.all([
          authApi.get("/admin/support/stats"),
        ]);
        setSupportStats(supportRes.data?.data ?? null);
      } catch (err: any) {
        setError(
          err?.response?.data?.msg ??
            err?.message ??
            "Failed to load overview data"
        );
      } finally {
        setLoading(false);
      }
    };
    void load();
  }, []);

  return (
    <Box>
      <Typography variant="h4" fontWeight={700} mb={0.5}>
        Overview
      </Typography>
      <Typography variant="body2" color="text.secondary" mb={3}>
        High-level snapshot for your recharge platform activity.
      </Typography>

      {error && (
        <Typography color="error" mb={2}>
          {error}
        </Typography>
      )}

      <Grid container spacing={2.5}>
        <Grid item xs={12} md={4}>
          <StatCard
            label="Open Support Chats"
            value={supportStats?.open_chats ?? "--"}
          />
        </Grid>
        <Grid item xs={12} md={4}>
          <StatCard
            label="Pending Support Chats"
            value={supportStats?.pending_chats ?? "--"}
          />
        </Grid>
        <Grid item xs={12} md={4}>
          <StatCard
            label="Resolved Today"
            value={supportStats?.resolved_today ?? "--"}
            accent={
              supportStats?.resolution_rate
                ? `Resolution rate ${supportStats.resolution_rate}%`
                : undefined
            }
          />
        </Grid>
      </Grid>

      {loading && (
        <Typography variant="body2" color="text.secondary" mt={3}>
          Loading latest data...
        </Typography>
      )}
    </Box>
  );
};


