import React, { useState, useEffect } from 'react';
import {
  Box,
  Card,
  CardContent,
  Typography,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  TextField,
  Button,
  Chip,
  IconButton,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Pagination,
  CircularProgress,
  Alert,
  Grid,
} from '@mui/material';
import { Search, Refresh, LocationOn, Language } from '@mui/icons-material';
import { apiService } from '../../services/api';

interface Location {
  country: string;
  state: string;
  city: string;
  postalCode: number;
  created_at: string;
  updated_at: string | null;
}

interface Language {
  system_language: string;
  system_language_name: string;
  app_language: string;
  app_language_name: string;
}

interface LocationUser {
  user_id: string;
  username: string;
  name: string;
  email: string | null;
  phone: string;
  location: Location;
  language: Language;
}

interface Filters {
  country?: string | null;
  state?: string | null;
  city?: string | null;
  postal_code?: string | null;
}

const LocationUsersPage: React.FC = () => {
  const [locations, setLocations] = useState<LocationUser[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [filters, setFilters] = useState<Filters>({});
  
  // Filter states
  const [country, setCountry] = useState('');
  const [state, setState] = useState('');
  const [city, setCity] = useState('');
  const [postalCode, setPostalCode] = useState('');
  
  // Pagination
  const [page, setPage] = useState(1);
  const [size, setSize] = useState(20);
  const [totalPages, setTotalPages] = useState(1);
  const [total, setTotal] = useState(0);

  const fetchLocationUsers = async () => {
    setLoading(true);
    setError(null);
    try {
      const response = await apiService.getLocationUsers({
        country: country || undefined,
        state: state || undefined,
        city: city || undefined,
        postal_code: postalCode ? parseInt(postalCode) : undefined,
        page,
        size,
      });
      
      if (response.data) {
        const data = response.data as any;
        setLocations(data.locations || []);
        
        setFilters({
          country: data.filters?.country || null,
          state: data.filters?.state || null,
          city: data.filters?.city || null,
          postal_code: data.filters?.postal_code || null,
        });
        
        setTotalPages(data.pagination?.total_pages || 1);
        setTotal(data.pagination?.total_count || 0);
      }
    } catch (err: any) {
      console.error('Location users fetch error:', err);
      setError(err.message || 'Failed to fetch location users');
      setLocations([]);
      setTotal(0);
      setTotalPages(1);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchLocationUsers();
  }, [page, size]);

  const handleSearch = () => {
    setPage(1);
    fetchLocationUsers();
  };

  const handleRefresh = () => {
    fetchLocationUsers();
  };

  const handleClearFilters = () => {
    setCountry('');
    setState('');
    setCity('');
    setPostalCode('');
    setPage(1);
  };

  return (
    <Box>
      <Typography variant="h4" gutterBottom>
        User Locations
      </Typography>
      <Typography variant="body2" color="textSecondary" sx={{ mb: 3 }}>
        View user locations and language preferences with filtering options
      </Typography>

      {error && (
        <Alert severity="error" sx={{ mb: 2 }} onClose={() => setError(null)}>
          {error}
        </Alert>
      )}

      {/* Filters */}
      <Card sx={{ mb: 3 }}>
        <CardContent>
          <Grid container spacing={2} alignItems="center">
            <Grid item xs={12} sm={6} md={2.5}>
              <TextField
                label="Country"
                variant="outlined"
                size="small"
                value={country}
                onChange={(e) => setCountry(e.target.value)}
                fullWidth
                placeholder="e.g., IN"
              />
            </Grid>

            <Grid item xs={12} sm={6} md={2.5}>
              <TextField
                label="State"
                variant="outlined"
                size="small"
                value={state}
                onChange={(e) => setState(e.target.value)}
                fullWidth
                placeholder="e.g., Rajasthan"
              />
            </Grid>

            <Grid item xs={12} sm={6} md={2.5}>
              <TextField
                label="City"
                variant="outlined"
                size="small"
                value={city}
                onChange={(e) => setCity(e.target.value)}
                fullWidth
                placeholder="e.g., Jaipur"
              />
            </Grid>

            <Grid item xs={12} sm={6} md={2.5}>
              <TextField
                label="Postal Code"
                variant="outlined"
                size="small"
                type="number"
                value={postalCode}
                onChange={(e) => setPostalCode(e.target.value)}
                fullWidth
                placeholder="e.g., 303030"
              />
            </Grid>

            <Grid item xs={12} sm={6} md={2}>
              <Box sx={{ display: 'flex', gap: 1 }}>
                <Button
                  variant="contained"
                  startIcon={<Search />}
                  onClick={handleSearch}
                  fullWidth
                >
                  Search
                </Button>
                <IconButton color="primary" onClick={handleRefresh} title="Refresh">
                  <Refresh />
                </IconButton>
              </Box>
            </Grid>
          </Grid>

          {(country || state || city || postalCode) && (
            <Box sx={{ mt: 2, display: 'flex', gap: 1, flexWrap: 'wrap', alignItems: 'center' }}>
              <Typography variant="body2" color="textSecondary">
                Active filters:
              </Typography>
              {country && (
                <Chip
                  label={`Country: ${country}`}
                  onDelete={() => setCountry('')}
                  size="small"
                  color="primary"
                />
              )}
              {state && (
                <Chip
                  label={`State: ${state}`}
                  onDelete={() => setState('')}
                  size="small"
                  color="primary"
                />
              )}
              {city && (
                <Chip
                  label={`City: ${city}`}
                  onDelete={() => setCity('')}
                  size="small"
                  color="primary"
                />
              )}
              {postalCode && (
                <Chip
                  label={`Postal Code: ${postalCode}`}
                  onDelete={() => setPostalCode('')}
                  size="small"
                  color="primary"
                />
              )}
              <Button size="small" onClick={handleClearFilters}>
                Clear All
              </Button>
            </Box>
          )}
        </CardContent>
      </Card>

      {/* Results Summary */}
      <Card sx={{ mb: 2 }}>
        <CardContent>
          <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', flexWrap: 'wrap', gap: 2 }}>
            <Typography variant="h6">
              Users ({total})
            </Typography>
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
              <Typography variant="body2" color="textSecondary">
                {total > 0 ? `Showing ${(page - 1) * size + 1} - ${Math.min(page * size, total)} of ${total}` : 'No users'}
              </Typography>
              <FormControl size="small" sx={{ minWidth: 100 }}>
                <InputLabel>Per Page</InputLabel>
                <Select
                  value={size}
                  label="Per Page"
                  onChange={(e) => {
                    setSize(Number(e.target.value));
                    setPage(1);
                  }}
                >
                  <MenuItem value={10}>10</MenuItem>
                  <MenuItem value={20}>20</MenuItem>
                  <MenuItem value={50}>50</MenuItem>
                  <MenuItem value={100}>100</MenuItem>
                </Select>
              </FormControl>
            </Box>
          </Box>
        </CardContent>
      </Card>

      {/* Table */}
      {loading ? (
        <Box sx={{ display: 'flex', justifyContent: 'center', p: 4 }}>
          <CircularProgress />
        </Box>
      ) : locations.length === 0 ? (
        <Card>
          <CardContent>
            <Typography color="textSecondary" align="center" sx={{ py: 4 }}>
              No location data found
            </Typography>
          </CardContent>
        </Card>
      ) : (
        <>
          <TableContainer component={Paper} variant="outlined">
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell>Name</TableCell>
                  <TableCell>Username</TableCell>
                  <TableCell>Email</TableCell>
                  <TableCell>Phone</TableCell>
                  <TableCell>
                    <Box sx={{ display: 'flex', alignItems: 'center', gap: 0.5 }}>
                      <LocationOn fontSize="small" />
                      Location
                    </Box>
                  </TableCell>
                  <TableCell>
                    <Box sx={{ display: 'flex', alignItems: 'center', gap: 0.5 }}>
                      <Language fontSize="small" />
                      Language
                    </Box>
                  </TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {locations.map((item) => (
                  <TableRow key={item.user_id} hover>
                    <TableCell>
                      <Typography variant="body2" fontWeight="medium">
                        {item.name || 'N/A'}
                      </Typography>
                    </TableCell>
                    <TableCell>
                      <Typography variant="body2" fontWeight="medium">
                        {item.username}
                      </Typography>
                    </TableCell>
                    <TableCell>{item.email || 'N/A'}</TableCell>
                    <TableCell>{item.phone}</TableCell>
                    <TableCell>
                      <Box>
                        <Chip label={item.location.country} size="small" sx={{ mb: 0.5, mr: 0.5 }} />
                        <Chip label={item.location.state} size="small" sx={{ mb: 0.5, mr: 0.5 }} />
                        <Chip label={item.location.city} size="small" sx={{ mb: 0.5, mr: 0.5 }} />
                        <Typography variant="caption" color="textSecondary" display="block">
                          PIN: {item.location.postalCode}
                        </Typography>
                      </Box>
                    </TableCell>
                    <TableCell>
                      <Box>
                        <Typography variant="caption" display="block" color="textSecondary">
                          System: {item.language.system_language_name} ({item.language.system_language})
                        </Typography>
                        <Typography variant="caption" display="block" color="textSecondary">
                          App: {item.language.app_language_name} ({item.language.app_language})
                        </Typography>
                      </Box>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>

          {totalPages > 1 && (
            <Box sx={{ display: 'flex', justifyContent: 'center', mt: 3 }}>
              <Pagination
                count={totalPages}
                page={page}
                onChange={(_, value) => setPage(value)}
                color="primary"
                showFirstButton
                showLastButton
                size="large"
              />
            </Box>
          )}
        </>
      )}
    </Box>
  );
};

export default LocationUsersPage;
