import React, { useState, useEffect } from 'react';
import {
  Box,
  Card,
  CardContent,
  Typography,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Button,
  Chip,
  IconButton,
  CircularProgress,
  Alert,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  FormControlLabel,
  Switch,
} from '@mui/material';
import { Send, Refresh } from '@mui/icons-material';
import { apiService } from '../../services/api';

interface FCMToken {
  id: number;
  user_id: string | null;
  admin_id: string | null;
  token_preview: string;
  token_length: number;
  device_type: string;
  device_name: string;
  app_version: string;
  is_active: boolean;
  last_used_at: string;
  created_at: string;
}

const FcmManagementPage: React.FC = () => {
  const [tokens, setTokens] = useState<FCMToken[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [success, setSuccess] = useState<string | null>(null);
  const [activeOnly, setActiveOnly] = useState(true);
  const [total, setTotal] = useState(0);
  const [testDialog, setTestDialog] = useState(false);
  const [testForm, setTestForm] = useState({
    user_id: '',
    token: '',
    title: 'Test Notification',
    body: 'This is a test notification from the admin panel',
    data: '',
  });

  const fetchTokens = async () => {
    setLoading(true);
    setError(null);
    try {
      const response = await apiService.getFCMTokens(activeOnly);
      
      if (response.data) {
        const data = response.data as any;
        setTokens(data.tokens || []);
        setTotal(data.total || 0);
      }
    } catch (err: any) {
      console.error('FCM tokens fetch error:', err);
      setError(err.message || 'Failed to fetch FCM tokens');
      setTokens([]);
      setTotal(0);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchTokens();
  }, [activeOnly]);

  const handleSendTest = async () => {
    try {
      const request: any = {
        title: testForm.title,
        body: testForm.body,
      };
      
      if (testForm.token) {
        request.token = testForm.token;
      } else if (testForm.user_id) {
        request.user_id = testForm.user_id;
      }
      
      if (testForm.data) {
        try {
          request.data = JSON.parse(testForm.data);
        } catch {
          setError('Invalid JSON in data field');
          return;
        }
      }
      
      await apiService.testFCMNotification(request);
      setSuccess('Test notification sent successfully');
      setTestDialog(false);
      setTestForm({
        user_id: '',
        token: '',
        title: 'Test Notification',
        body: 'This is a test notification from the admin panel',
        data: '',
      });
    } catch (err: any) {
      setError(err.message || 'Failed to send test notification');
    }
  };

  return (
    <Box>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
        <Typography variant="h4">FCM Token Management</Typography>
        <Button
          variant="contained"
          startIcon={<Send />}
          onClick={() => setTestDialog(true)}
        >
          Send Test Notification
        </Button>
      </Box>

      <Card sx={{ mb: 3 }}>
        <CardContent>
          <Box sx={{ display: 'flex', gap: 2, alignItems: 'center' }}>
            <FormControlLabel
              control={
                <Switch
                  checked={activeOnly}
                  onChange={(e) => setActiveOnly(e.target.checked)}
                />
              }
              label="Active Only"
            />
            <IconButton onClick={fetchTokens} color="primary">
              <Refresh />
            </IconButton>
          </Box>
        </CardContent>
      </Card>

      {error && (
        <Alert severity="error" sx={{ mb: 2 }} onClose={() => setError(null)}>
          {error}
        </Alert>
      )}

      {success && (
        <Alert severity="success" sx={{ mb: 2 }} onClose={() => setSuccess(null)}>
          {success}
        </Alert>
      )}

      <Card>
        <CardContent>
          <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
            <Typography variant="h6">
              FCM Tokens ({total})
            </Typography>
          </Box>

          {loading ? (
            <Box sx={{ display: 'flex', justifyContent: 'center', p: 4 }}>
              <CircularProgress />
            </Box>
          ) : (
            <>
              <TableContainer component={Paper} variant="outlined">
                <Table>
                  <TableHead>
                    <TableRow>
                      <TableCell>Token Preview</TableCell>
                      <TableCell>User/Admin ID</TableCell>
                      <TableCell>Device Type</TableCell>
                      <TableCell>Device Name</TableCell>
                      <TableCell>App Version</TableCell>
                      <TableCell>Status</TableCell>
                      <TableCell>Last Used</TableCell>
                      <TableCell>Created At</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {tokens.length === 0 ? (
                      <TableRow>
                        <TableCell colSpan={8} align="center">
                          <Typography color="textSecondary">No FCM tokens found</Typography>
                        </TableCell>
                      </TableRow>
                    ) : (
                      tokens.map((token) => (
                        <TableRow key={token.id} hover>
                          <TableCell>
                            <Typography variant="body2" sx={{ fontFamily: 'monospace', fontSize: '0.75rem' }}>
                              {token.token_preview}
                            </Typography>
                            <Typography variant="caption" color="textSecondary">
                              Length: {token.token_length}
                            </Typography>
                          </TableCell>
                          <TableCell>
                            {token.user_id ? (
                              <Typography variant="body2" sx={{ fontFamily: 'monospace', fontSize: '0.75rem' }}>
                                User: {token.user_id.substring(0, 8)}...
                              </Typography>
                            ) : token.admin_id ? (
                              <Typography variant="body2" sx={{ fontFamily: 'monospace', fontSize: '0.75rem' }}>
                                Admin: {token.admin_id.substring(0, 8)}...
                              </Typography>
                            ) : (
                              'N/A'
                            )}
                          </TableCell>
                          <TableCell>
                            <Chip label={token.device_type} size="small" variant="outlined" />
                          </TableCell>
                          <TableCell>{token.device_name}</TableCell>
                          <TableCell>
                            <Chip label={token.app_version} size="small" color="primary" variant="outlined" />
                          </TableCell>
                          <TableCell>
                            <Chip
                              label={token.is_active ? 'Active' : 'Inactive'}
                              color={token.is_active ? 'success' : 'default'}
                              size="small"
                            />
                          </TableCell>
                          <TableCell>
                            {token.last_used_at ? (
                              <Typography variant="body2" fontSize="0.75rem">
                                {new Date(token.last_used_at).toLocaleString()}
                              </Typography>
                            ) : (
                              'Never'
                            )}
                          </TableCell>
                          <TableCell>
                            {new Date(token.created_at).toLocaleString()}
                          </TableCell>
                        </TableRow>
                      ))
                    )}
                  </TableBody>
                </Table>
              </TableContainer>
            </>
          )}
        </CardContent>
      </Card>

      <Dialog open={testDialog} onClose={() => setTestDialog(false)} maxWidth="sm" fullWidth>
        <DialogTitle>Send Test Notification</DialogTitle>
        <DialogContent>
          <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2, mt: 1 }}>
            <TextField
              label="User ID (Optional)"
              fullWidth
              value={testForm.user_id}
              onChange={(e) => setTestForm({ ...testForm, user_id: e.target.value })}
              helperText="Leave empty to send to yourself (admin)"
            />
            <TextField
              label="Token (Optional)"
              fullWidth
              value={testForm.token}
              onChange={(e) => setTestForm({ ...testForm, token: e.target.value })}
              helperText="Direct FCM token (highest priority)"
            />
            <TextField
              label="Title"
              fullWidth
              value={testForm.title}
              onChange={(e) => setTestForm({ ...testForm, title: e.target.value })}
            />
            <TextField
              label="Body"
              fullWidth
              multiline
              rows={3}
              value={testForm.body}
              onChange={(e) => setTestForm({ ...testForm, body: e.target.value })}
            />
            <TextField
              label="Data (JSON, Optional)"
              fullWidth
              multiline
              rows={3}
              value={testForm.data}
              onChange={(e) => setTestForm({ ...testForm, data: e.target.value })}
              helperText="Additional data payload as JSON"
            />
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setTestDialog(false)}>Cancel</Button>
          <Button onClick={handleSendTest} variant="contained">
            Send
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default FcmManagementPage;

