import {
  Box,
  Card,
  CardContent,
  Typography,
  TextField,
  Button,
  Alert,
  CircularProgress,
  Autocomplete,
  Chip,
  Divider,
  Paper,
  Tabs,
  Tab,
  Grid,
  IconButton,
} from '@mui/material';
import { Send, Person, Message, ContentCopy, CheckCircle } from '@mui/icons-material';
import { apiService } from '../../services/api';

