const NotificationsPage: React.FC = () => {
  const [selectedUser, setSelectedUser] = useState<User | null>(null);
  const [userId, setUserId] = useState('');
  const [message, setMessage] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [success, setSuccess] = useState<string | null>(null);
  const [users, setUsers] = useState<User[]>([]);
  const [usersLoading, setUsersLoading] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');
  const [selectedCategory, setSelectedCategory] = useState(0);
  const [selectedPreset, setSelectedPreset] = useState<string | null>(null);

  // Fetch users for autocomplete
  const fetchUsers = async (search: string = '') => {
    if (search.length < 2) {
      setUsers([]);
      return;
    }

    setUsersLoading(true);
    try {
      const response = await apiService.getUsers({
        search: search,
        page: 1,
        size: 20,
      });
      
      if (response.data) {
        const data = response.data as any;
        setUsers(data.items || []);
      }
    } catch (err) {
      console.error('Failed to fetch users:', err);
      setUsers([]);
    } finally {
      setUsersLoading(false);
    }
  };

  useEffect(() => {
    const debounceTimer = setTimeout(() => {
      if (searchQuery) {
        fetchUsers(searchQuery);
      } else {
        setUsers([]);
      }
    }, 300);

    return () => clearTimeout(debounceTimer);
  }, [searchQuery]);

  const handleSend = async () => {
    if (!userId.trim()) {
      setError('Please enter or select a user ID');
      return;
    }

    if (!message.trim()) {
      setError('Please enter a message');
      return;
    }

    setLoading(true);
    setError(null);
    setSuccess(null);

    try {
      await apiService.sendNotification(userId.trim(), message.trim());
      setSuccess('Notification sent successfully!');
      setMessage('');
      setSelectedUser(null);
      setUserId('');
      setSearchQuery('');
      setSelectedPreset(null);
    } catch (err: any) {
      setError(err.message || 'Failed to send notification');
    } finally {
      setLoading(false);
    }
  };

  const handleUserSelect = (user: User | null) => {
    setSelectedUser(user);
    if (user) {
      setUserId(user.id);
      setSearchQuery(user.username || user.email || user.phone);
    } else {
      setUserId('');
      setSearchQuery('');
    }
  };

  return (
    <Box>
      <Typography variant="h4" gutterBottom>
        Push Notifications
      </Typography>
      <Typography variant="body2" color="textSecondary" sx={{ mb: 3 }}>
        Send push notifications to users on their smartphones
      </Typography>

      {error && (
        <Alert severity="error" sx={{ mb: 2 }} onClose={() => setError(null)}>
          {error}
        </Alert>
      )}

      {success && (
        <Alert severity="success" sx={{ mb: 2 }} onClose={() => setSuccess(null)}>
          {success}
        </Alert>
      )}

      <Card>
        <CardContent>
          <Box sx={{ display: 'flex', flexDirection: 'column', gap: 3 }}>
            {/* User Selection */}
            <Box>
              <Typography variant="h6" gutterBottom sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                <Person color="primary" />
                Select User
              </Typography>
              <Typography variant="body2" color="textSecondary" sx={{ mb: 2 }}>
                Search for a user by username, email, or phone number, or enter a user ID directly
              </Typography>
              
              <Box sx={{ display: 'flex', gap: 2, flexWrap: 'wrap' }}>
                <Autocomplete
                  sx={{ flexGrow: 1, minWidth: 300 }}
                  options={users}
                  getOptionLabel={(option) => 
                    `${option.username}${option.email ? ` (${option.email})` : ''} - ${option.phone}`
                  }
                  loading={usersLoading}
                  value={selectedUser}
                  onChange={(_, newValue) => handleUserSelect(newValue)}
                  inputValue={searchQuery}
                  onInputChange={(_, newInputValue) => {
                    setSearchQuery(newInputValue);
                    if (!newInputValue) {
                      setSelectedUser(null);
                      setUserId('');
                    }
                  }}
                  renderInput={(params) => (
                    <TextField
                      {...params}
                      label="Search User"
                      placeholder="Type username, email, or phone..."
                      variant="outlined"
                    />
                  )}
                  renderOption={(props, option) => (
                    <Box component="li" {...props} key={option.id}>
                      <Box sx={{ display: 'flex', flexDirection: 'column', width: '100%' }}>
                        <Typography variant="body1" fontWeight="medium">
                          {option.username}
                        </Typography>
                        <Box sx={{ display: 'flex', gap: 1, mt: 0.5 }}>
                          {option.email && (
                            <Chip label={option.email} size="small" variant="outlined" />
                          )}
                          <Chip label={option.phone} size="small" variant="outlined" />
                          <Chip 
                            label={`ID: ${option.id.substring(0, 8)}...`} 
                            size="small" 
                            variant="outlined"
                            sx={{ fontFamily: 'monospace', fontSize: '0.7rem' }}
                          />
                        </Box>
                      </Box>
                    </Box>
                  )}
                  noOptionsText={searchQuery.length < 2 ? 'Type at least 2 characters to search' : 'No users found'}
                />

                <TextField
                  label="Or Enter User ID"
                  variant="outlined"
                  value={userId}
                  onChange={(e) => {
                    setUserId(e.target.value);
                    if (e.target.value) {
                      setSelectedUser(null);
                      setSearchQuery('');
                    }
                  }}
                  placeholder="User ID"
                  sx={{ minWidth: 250 }}
                  helperText="Enter user ID directly if you know it"
                />
              </Box>

              {selectedUser && (
                <Box sx={{ mt: 2, p: 2, bgcolor: 'action.hover', borderRadius: 1 }}>
                  <Typography variant="body2" color="textSecondary" gutterBottom>
                    Selected User:
                  </Typography>
                  <Box sx={{ display: 'flex', gap: 1, flexWrap: 'wrap', mt: 1 }}>
                    <Chip label={`Username: ${selectedUser.username}`} size="small" />
                    {selectedUser.email && (
                      <Chip label={`Email: ${selectedUser.email}`} size="small" />
                    )}
                    <Chip label={`Phone: ${selectedUser.phone}`} size="small" />
                    <Chip 
                      label={`ID: ${selectedUser.id}`} 
                      size="small" 
                      sx={{ fontFamily: 'monospace' }}
                    />
                  </Box>
                </Box>
              )}
            </Box>

            <Divider />

            {/* Message Section with Presets */}
            <Box>
              <Typography variant="h6" gutterBottom sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                <Message color="primary" />
                Notification Message
              </Typography>
              <Typography variant="body2" color="textSecondary" sx={{ mb: 2 }}>
                Choose a preset message or type your own custom message
              </Typography>
              
              <Grid container spacing={3}>
                {/* Preset Messages - Left Side */}
                <Grid item xs={12} md={5}>
                  <Card variant="outlined" sx={{ height: '100%' }}>
                    <CardContent>
                      <Typography variant="subtitle1" fontWeight="bold" gutterBottom>
                         Quick Messages
                      </Typography>
                      <Typography variant="caption" color="textSecondary" display="block" sx={{ mb: 2 }}>
                        Select a category and choose a message
                      </Typography>
                      
                      <Tabs
                        value={selectedCategory}
                        onChange={(_, newValue) => setSelectedCategory(newValue)}
                        variant="scrollable"
                        scrollButtons="auto"
                        sx={{
                          borderBottom: 1,
                          borderColor: 'divider',
                          mb: 2,
                          '& .MuiTab-root': {
                            minHeight: 48,
                            fontSize: '0.75rem',
                            textTransform: 'none',
                          },
                        }}
                      >
                        {MESSAGE_PRESETS.map((preset, index) => (
                          <Tab
                            key={index}
                            label={
                              <Box sx={{ display: 'flex', alignItems: 'center', gap: 0.5 }}>
                                <span>{preset.icon}</span>
                                <span style={{ fontSize: '0.7rem' }}>
                                  {preset.category.split(' ')[0]}
                                </span>
                              </Box>
                            }
                          />
                        ))}
                      </Tabs>

                      <Box sx={{ maxHeight: 400, overflowY: 'auto', pr: 1 }}>
                        {MESSAGE_PRESETS[selectedCategory] && (
                          <Box sx={{ display: 'flex', flexDirection: 'column', gap: 1.5 }}>
                            {MESSAGE_PRESETS[selectedCategory].messages.map((msg, msgIndex) => {
                              const isSelected = selectedPreset === msg;
                              return (
                                <Paper
                                  key={msgIndex}
                                  elevation={isSelected ? 3 : 0}
                                  sx={{
                                    p: 2,
                                    cursor: 'pointer',
                                    border: '2px solid',
                                    borderColor: isSelected ? 'primary.main' : 'divider',
                                    borderRadius: 2,
                                    bgcolor: isSelected ? 'primary.light' : 'background.paper',
                                    transition: 'all 0.2s',
                                    '&:hover': {
                                      borderColor: 'primary.main',
                                      bgcolor: isSelected ? 'primary.light' : 'action.hover',
                                      transform: 'translateX(4px)',
                                    },
                                  }}
                                  onClick={() => {
                                    setMessage(msg);
                                    setSelectedPreset(msg);
                                  }}
                                >
                                  <Box sx={{ display: 'flex', alignItems: 'flex-start', gap: 1 }}>
                                    {isSelected && (
                                      <CheckCircle color="primary" sx={{ mt: 0.5, fontSize: 20 }} />
                                    )}
                                    <Typography
                                      variant="body2"
                                      sx={{
                                        flexGrow: 1,
                                        wordBreak: 'break-word',
                                        fontWeight: isSelected ? 500 : 400,
                                      }}
                                    >
                                      {msg}
                                    </Typography>
                                    <IconButton
                                      size="small"
                                      onClick={(e) => {
                                        e.stopPropagation();
                                        navigator.clipboard.writeText(msg);
                                      }}
                                      sx={{ opacity: 0.6, '&:hover': { opacity: 1 } }}
                                    >
                                      <ContentCopy fontSize="small" />
                                    </IconButton>
                                  </Box>
                                </Paper>
                              );
                            })}
                          </Box>
                        )}
                      </Box>
                    </CardContent>
                  </Card>
                </Grid>

                {/* Message Input - Right Side */}
                <Grid item xs={12} md={7}>
                  <Card variant="outlined" sx={{ height: '100%' }}>
                    <CardContent>
                      <Typography variant="subtitle1" fontWeight="bold" gutterBottom>
                         Your Message
                      </Typography>
                      <Typography variant="caption" color="textSecondary" display="block" sx={{ mb: 2 }}>
                        Edit the selected message or type your own
                      </Typography>
                      
                      <TextField
                        label="Message"
                        multiline
                        rows={10}
                        fullWidth
                        value={message}
                        onChange={(e) => {
                          setMessage(e.target.value);
                          if (e.target.value !== selectedPreset) {
                            setSelectedPreset(null);
                          }
                        }}
                        placeholder="Select a preset message from the left or type your own custom message..."
                        variant="outlined"
                        helperText={`${message.length} characters`}
                        sx={{
                          '& .MuiOutlinedInput-root': {
                            fontSize: '0.95rem',
                            lineHeight: 1.6,
                          },
                        }}
                      />
                    </CardContent>
                  </Card>
                </Grid>
              </Grid>
            </Box>

            <Divider />

            {/* Send Button */}
            <Box sx={{ display: 'flex', justifyContent: 'flex-end', gap: 2 }}>
              <Button
                variant="outlined"
                onClick={() => {
                  setMessage('');
                  setSelectedUser(null);
                  setUserId('');
                  setSearchQuery('');
                  setSelectedPreset(null);
                  setError(null);
                  setSuccess(null);
                }}
                disabled={loading}
              >
                Clear All
              </Button>
              <Button
                variant="contained"
                size="large"
                startIcon={loading ? <CircularProgress size={20} color="inherit" /> : <Send />}
                onClick={handleSend}
                disabled={loading || !userId.trim() || !message.trim()}
                sx={{ minWidth: 150 }}
              >
                {loading ? 'Sending...' : 'Send Notification'}
              </Button>
            </Box>
          </Box>
        </CardContent>
      </Card>

      {/* Help Card */}
      <Card sx={{ mt: 3 }}>
        <CardContent>
          <Typography variant="h6" gutterBottom>
            How to Send Notifications
          </Typography>
          <Box component="ul" sx={{ pl: 2, m: 0 }}>
            <Typography component="li" variant="body2" color="textSecondary" sx={{ mb: 1 }}>
              Search for a user by typing their username, email, or phone number in the search box
            </Typography>
            <Typography component="li" variant="body2" color="textSecondary" sx={{ mb: 1 }}>
              Or enter the user ID directly in the "User ID" field if you know it
            </Typography>
            <Typography component="li" variant="body2" color="textSecondary" sx={{ mb: 1 }}>
              <strong>Select a preset message</strong> from the categories below, or type your own custom message
            </Typography>
            <Typography component="li" variant="body2" color="textSecondary" sx={{ mb: 1 }}>
              You can edit preset messages after selecting them
            </Typography>
            <Typography component="li" variant="body2" color="textSecondary">
              Click "Send Notification" to deliver the push notification to the user's device
            </Typography>
          </Box>
        </CardContent>
      </Card>
    </Box>
  );
};

export default NotificationsPage;

