import React, { useState, useEffect } from 'react';
import {
  Box,
  Card,
  CardContent,
  Typography,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  TextField,
  Button,
  Chip,
  IconButton,
  MenuItem,
  Select,
  FormControl,
  InputLabel,
  Pagination,
  CircularProgress,
  Alert,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  DialogContentText,
  Avatar,
  Tooltip,
  Divider,
} from '@mui/material';
import { Search, Refresh, CheckCircle, Cancel, Visibility } from '@mui/icons-material';
import { apiService } from '../../services/api';
import { SlideInPanel, DetailRow, ImageViewer } from '../../components/SlideInPanel';

interface FundRequest {
  id: number;
  txnid: string;
  user_id: string;
  username: string;
  wallet_type: string;
  amount: string;
  status: string;
  ref_id: string;
  txn_date: string;
  txn_proof?: string;
  remark?: string;
  created_at: string;
  updated_at: string;
}

const FundRequestsPage: React.FC = () => {
  const [requests, setRequests] = useState<FundRequest[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [success, setSuccess] = useState<string | null>(null);
  const [userId, setUserId] = useState('');
  const [status, setStatus] = useState('');
  const [walletType, setWalletType] = useState('');
  const [refId, setRefId] = useState('');
  const [fromDate, setFromDate] = useState('');
  const [toDate, setToDate] = useState('');
  const [page, setPage] = useState(1);
  const [size, setSize] = useState(20);
  const [totalPages, setTotalPages] = useState(1);
  const [total, setTotal] = useState(0);
  const [baseUrl, setBaseUrl] = useState('');
  const [processDialog, setProcessDialog] = useState<{ open: boolean; request: FundRequest | null }>({
    open: false,
    request: null,
  });
  const [action, setAction] = useState<'accept' | 'reject'>('accept');
  const [remark, setRemark] = useState('');
  const [detailPanel, setDetailPanel] = useState<{ open: boolean; request: FundRequest | null }>({
    open: false,
    request: null,
  });

  const fetchRequests = async () => {
    setLoading(true);
    setError(null);
    try {
      const response = await apiService.getFundRequests({
        user_id: userId || undefined,
        status: status || undefined,
        wallet_type: walletType || undefined,
        ref_id: refId || undefined,
        from_date: fromDate || undefined,
        to_date: toDate || undefined,
        page,
        size,
      });
      
      if (response.data) {
        const data = response.data as any;
        setRequests(data.items || []);
        setTotalPages(data.pages || data.total_pages || 1);
        setTotal(data.total || 0);
        if (data.base_url) {
          setBaseUrl(data.base_url);
        }
      }
    } catch (err: any) {
      console.error('Fund requests fetch error:', err);
      setError(err.message || 'Failed to fetch fund requests');
      setRequests([]);
      setTotal(0);
      setTotalPages(1);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchRequests();
  }, [page, size]);

  const handleSearch = () => {
    setPage(1);
    fetchRequests();
  };

  const handleRefresh = () => {
    fetchRequests();
  };

  const handleProcess = async () => {
    if (!processDialog.request) return;
    
    try {
      const apiAction = action === 'accept' ? 'accept' : 'reject';
      await apiService.processFundRequest(processDialog.request.txnid, apiAction, remark || undefined);
      setSuccess(`Fund request ${action === 'accept' ? 'accepted' : 'rejected'} successfully`);
      setProcessDialog({ open: false, request: null });
      setRemark('');
      fetchRequests();
    } catch (err: any) {
      setError(err.message || 'Failed to process fund request');
    }
  };

  const getStatusColor = (status: string) => {
    switch (status.toLowerCase()) {
      case 'approved':
        return 'success';
      case 'pending':
        return 'warning';
      case 'rejected':
        return 'error';
      default:
        return 'default';
    }
  };

  return (
    <Box>
      <Typography variant="h4" gutterBottom>
        Fund Requests
      </Typography>

      <Card sx={{ mb: 3 }}>
        <CardContent>
          <Box sx={{ display: 'flex', gap: 2, flexWrap: 'wrap', alignItems: 'center' }}>
            <TextField
              label="User ID"
              variant="outlined"
              size="small"
              value={userId}
              onChange={(e) => setUserId(e.target.value)}
              sx={{ minWidth: 200 }}
            />
            <FormControl size="small" sx={{ minWidth: 150 }}>
              <InputLabel>Status</InputLabel>
              <Select
                value={status}
                label="Status"
                onChange={(e) => setStatus(e.target.value)}
              >
                <MenuItem value="">All</MenuItem>
                <MenuItem value="pending">Pending</MenuItem>
                <MenuItem value="approved">Approved</MenuItem>
                <MenuItem value="rejected">Rejected</MenuItem>
              </Select>
            </FormControl>
            <TextField
              label="Wallet Type"
              variant="outlined"
              size="small"
              value={walletType}
              onChange={(e) => setWalletType(e.target.value)}
              sx={{ minWidth: 150 }}
            />
            <TextField
              label="Ref ID"
              variant="outlined"
              size="small"
              value={refId}
              onChange={(e) => setRefId(e.target.value)}
              sx={{ minWidth: 150 }}
            />
            <TextField
              label="From Date"
              type="date"
              variant="outlined"
              size="small"
              value={fromDate}
              onChange={(e) => setFromDate(e.target.value)}
              InputLabelProps={{ shrink: true }}
              sx={{ minWidth: 150 }}
            />
            <TextField
              label="To Date"
              type="date"
              variant="outlined"
              size="small"
              value={toDate}
              onChange={(e) => setToDate(e.target.value)}
              InputLabelProps={{ shrink: true }}
              sx={{ minWidth: 150 }}
            />
            <Button
              variant="contained"
              startIcon={<Search />}
              onClick={handleSearch}
            >
              Search
            </Button>
            <IconButton onClick={handleRefresh} color="primary">
              <Refresh />
            </IconButton>
          </Box>
        </CardContent>
      </Card>

      {error && (
        <Alert severity="error" sx={{ mb: 2 }} onClose={() => setError(null)}>
          {error}
        </Alert>
      )}

      {success && (
        <Alert severity="success" sx={{ mb: 2 }} onClose={() => setSuccess(null)}>
          {success}
        </Alert>
      )}

      <Card>
        <CardContent>
          <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2, flexWrap: 'wrap', gap: 2 }}>
            <Typography variant="h6">
              Fund Requests ({total})
            </Typography>
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
              <Typography variant="body2" color="textSecondary">
                {total > 0 ? (
                  `Showing ${(page - 1) * size + 1} - ${Math.min(page * size, total)} of ${total}`
                ) : (
                  'No fund requests'
                )}
              </Typography>
              <FormControl size="small" sx={{ minWidth: 100 }}>
                <InputLabel>Per Page</InputLabel>
                <Select
                  value={size}
                  label="Per Page"
                  onChange={(e) => {
                    setSize(Number(e.target.value));
                    setPage(1);
                  }}
                >
                  <MenuItem value={10}>10</MenuItem>
                  <MenuItem value={20}>20</MenuItem>
                  <MenuItem value={50}>50</MenuItem>
                  <MenuItem value={100}>100</MenuItem>
                </Select>
              </FormControl>
            </Box>
          </Box>

          {loading ? (
            <Box sx={{ display: 'flex', justifyContent: 'center', p: 4 }}>
              <CircularProgress />
            </Box>
          ) : (
            <>
              <TableContainer component={Paper} variant="outlined">
                <Table>
                  <TableHead>
                    <TableRow>
                      <TableCell>Txn ID</TableCell>
                      <TableCell>Username</TableCell>
                      <TableCell>User ID</TableCell>
                      <TableCell>Wallet Type</TableCell>
                      <TableCell>Amount</TableCell>
                      <TableCell>Ref ID</TableCell>
                      <TableCell>Status</TableCell>
                      <TableCell>Txn Date</TableCell>
                      <TableCell>Proof</TableCell>
                      <TableCell>Created At</TableCell>
                      <TableCell>Actions</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {requests.length === 0 ? (
                      <TableRow>
                        <TableCell colSpan={11} align="center">
                          <Typography color="textSecondary">No fund requests found</Typography>
                        </TableCell>
                      </TableRow>
                    ) : (
                      requests.map((request) => (
                        <TableRow key={request.id} hover>
                          <TableCell>
                            <Typography
                              variant="body2"
                              sx={{
                                fontFamily: 'monospace',
                                fontSize: '0.75rem',
                                cursor: 'pointer',
                                color: 'primary.main',
                                textDecoration: 'underline',
                                '&:hover': {
                                  color: 'primary.dark',
                                },
                              }}
                              onClick={(e) => {
                                e.stopPropagation();
                                setDetailPanel({ open: true, request });
                              }}
                            >
                              {request.txnid}
                            </Typography>
                          </TableCell>
                          <TableCell>
                            <Typography variant="body2" fontWeight="medium">
                              {request.username}
                            </Typography>
                          </TableCell>
                          <TableCell>
                            <Typography variant="body2" sx={{ fontFamily: 'monospace', fontSize: '0.75rem' }}>
                              {request.user_id.substring(0, 8)}...
                            </Typography>
                          </TableCell>
                          <TableCell>
                            <Chip label={request.wallet_type} size="small" />
                          </TableCell>
                          <TableCell>
                            <Typography variant="body2" fontWeight="bold">
                              {parseFloat(request.amount || '0').toFixed(2)}
                            </Typography>
                          </TableCell>
                          <TableCell>{request.ref_id}</TableCell>
                          <TableCell>
                            {(request.status.toLowerCase() === 'approved' || request.status.toLowerCase() === 'rejected') && request.remark ? (
                              <Tooltip 
                                title={
                                  <Box>
                                    <Typography variant="caption" fontWeight="bold" display="block" sx={{ mb: 0.5 }}>
                                      Remark:
                                    </Typography>
                                    <Typography variant="caption">
                                      {request.remark}
                                    </Typography>
                                  </Box>
                                }
                                arrow
                                placement="top"
                              >
                                <Chip
                                  label={request.status}
                                  color={getStatusColor(request.status) as any}
                                  size="small"
                                  sx={{ cursor: 'help' }}
                                />
                              </Tooltip>
                            ) : (
                              <Chip
                                label={request.status}
                                color={getStatusColor(request.status) as any}
                                size="small"
                              />
                            )}
                          </TableCell>
                          <TableCell>
                            {new Date(request.txn_date).toLocaleDateString()}
                          </TableCell>
                          <TableCell>
                            {request.txn_proof ? (
                              <IconButton
                                size="small"
                                onClick={() => {
                                  const imageUrl = request.txn_proof?.startsWith('http') 
                                    ? request.txn_proof 
                                    : `${baseUrl}${request.txn_proof?.startsWith('/') ? '' : '/'}${request.txn_proof}`;
                                  window.open(imageUrl, '_blank');
                                }}
                              >
                                <Visibility />
                              </IconButton>
                            ) : (
                              'N/A'
                            )}
                          </TableCell>
                          <TableCell>
                            {new Date(request.created_at).toLocaleString()}
                          </TableCell>
                          <TableCell>
                            {request.status === 'pending' && (
                              <Box sx={{ display: 'flex', gap: 1 }}>
                                <IconButton
                                  size="small"
                                  color="success"
                                  onClick={() => {
                                    setAction('accept');
                                    setProcessDialog({ open: true, request });
                                  }}
                                >
                                  <CheckCircle />
                                </IconButton>
                                <IconButton
                                  size="small"
                                  color="error"
                                  onClick={() => {
                                    setAction('reject');
                                    setProcessDialog({ open: true, request });
                                  }}
                                >
                                  <Cancel />
                                </IconButton>
                              </Box>
                            )}
                          </TableCell>
                        </TableRow>
                      ))
                    )}
                  </TableBody>
                </Table>
              </TableContainer>

              {total > 0 && (
                <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mt: 3, flexWrap: 'wrap', gap: 2 }}>
                  <Typography variant="body2" color="textSecondary">
                    Page {page} of {totalPages}
                  </Typography>
                  <Pagination
                    count={totalPages}
                    page={page}
                    onChange={(_, newPage) => setPage(newPage)}
                    color="primary"
                    showFirstButton
                    showLastButton
                    size="large"
                  />
                </Box>
              )}
            </>
          )}
        </CardContent>
      </Card>

      <Dialog open={processDialog.open} onClose={() => setProcessDialog({ open: false, request: null })}>
        <DialogTitle>
          {action === 'accept' ? 'Accept' : 'Reject'} Fund Request
        </DialogTitle>
        <DialogContent>
          <DialogContentText sx={{ mb: 2 }}>
            {action === 'accept' ? 'Accept' : 'Reject'} fund request: {processDialog.request?.txnid}
            <br />
            Amount: {processDialog.request ? parseFloat(processDialog.request.amount || '0').toFixed(2) : '0.00'}
          </DialogContentText>
          <TextField
            fullWidth
            label="Remark (Optional)"
            multiline
            rows={3}
            value={remark}
            onChange={(e) => setRemark(e.target.value)}
            sx={{ mt: 2 }}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setProcessDialog({ open: false, request: null })}>Cancel</Button>
          <Button
            onClick={handleProcess}
            variant="contained"
            color={action === 'accept' ? 'success' : 'error'}
          >
            {action === 'accept' ? 'Accept' : 'Reject'}
          </Button>
        </DialogActions>
      </Dialog>

      {/* Transaction Detail Slide-In Panel */}
      <SlideInPanel
        open={detailPanel.open}
        onClose={() => setDetailPanel({ open: false, request: null })}
        title="Transaction Details"
        width={600}
      >
        {detailPanel.request ? (
            <>
              {/* Transaction Proof Image */}
              {detailPanel.request.txn_proof && (
                <ImageViewer
                  imageUrl={
                    detailPanel.request.txn_proof.startsWith('http')
                      ? detailPanel.request.txn_proof
                      : `${baseUrl}${detailPanel.request.txn_proof.startsWith('/') ? '' : '/'}${detailPanel.request.txn_proof}`
                  }
                  alt="Transaction Proof"
                  title="Transaction Proof"
                />
              )}

              <Divider sx={{ my: 2 }} />

              {/* Transaction Details */}
              <Typography variant="subtitle1" fontWeight="bold" gutterBottom>
                Transaction Information
              </Typography>

              <DetailRow label="Transaction ID" value={detailPanel.request.txnid} copyable />
              <DetailRow label="Reference ID" value={detailPanel.request.ref_id} copyable />
              <DetailRow
                label="Amount"
                value={
                  <Typography variant="body2" fontWeight="bold" color="primary.main">
                    {parseFloat(detailPanel.request.amount || '0').toFixed(2)}
                  </Typography>
                }
              />
              <DetailRow
                label="Status"
                value={
                  <Chip
                    label={detailPanel.request.status}
                    color={getStatusColor(detailPanel.request.status) as any}
                    size="small"
                  />
                }
              />
              {detailPanel.request.remark && (
                <DetailRow
                  label="Remark"
                  value={
                    <Typography variant="body2" sx={{ fontStyle: 'italic' }}>
                      {detailPanel.request.remark}
                    </Typography>
                  }
                />
              )}

              <Divider sx={{ my: 2 }} />

              {/* User Information */}
              <Typography variant="subtitle1" fontWeight="bold" gutterBottom>
                User Information
              </Typography>

              <DetailRow label="Username" value={detailPanel.request.username} />
              <DetailRow label="User ID" value={detailPanel.request.user_id} copyable />
              <DetailRow
                label="Wallet Type"
                value={<Chip label={detailPanel.request.wallet_type} size="small" />}
              />

              <Divider sx={{ my: 2 }} />

              {/* Dates */}
              <Typography variant="subtitle1" fontWeight="bold" gutterBottom>
                Dates
              </Typography>

              <DetailRow
                label="Transaction Date"
                value={new Date(detailPanel.request.txn_date).toLocaleString()}
              />
              <DetailRow
                label="Created At"
                value={new Date(detailPanel.request.created_at).toLocaleString()}
              />
              <DetailRow
                label="Updated At"
                value={new Date(detailPanel.request.updated_at).toLocaleString()}
              />
            </>
          ) : (
            <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', minHeight: 200 }}>
              <Typography color="textSecondary">Loading transaction details...</Typography>
            </Box>
          )}
      </SlideInPanel>
    </Box>
  );
};

export default FundRequestsPage;

