import React, { useState, useEffect } from 'react';
import {
  Box,
  Card,
  CardContent,
  Typography,
  Button,
  Chip,
  IconButton,
  MenuItem,
  Select,
  FormControl,
  InputLabel,
  Pagination,
  CircularProgress,
  Alert,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Grid,
  Paper,
} from '@mui/material';
import { Add, Edit, Delete, Refresh } from '@mui/icons-material';
import { apiService } from '../../services/api';

interface Offer {
  id: number;
  title: string;
  subtitle: string | null;
  desc: string | null;
  image: string;
  active: boolean;
  from_date: string | null;
  to_date: string | null;
  created_at: string;
  updated_at: string | null;
}

const API_BASE_URL = import.meta.env.VITE_API_BASE_URL || 'https://api.laddooapp.com';

const OffersPage: React.FC = () => {
  const [offers, setOffers] = useState<Offer[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [success, setSuccess] = useState<string | null>(null);
  const [activeFilter, setActiveFilter] = useState<boolean | null>(null);
  const [page, setPage] = useState(1);
  const [size, setSize] = useState(20);
  const [totalPages, setTotalPages] = useState(1);
  const [total, setTotal] = useState(0);
  const [dialogOpen, setDialogOpen] = useState(false);
  const [editingOffer, setEditingOffer] = useState<Offer | null>(null);
  const [deleteDialog, setDeleteDialog] = useState<{ open: boolean; offerId: number | null }>({
    open: false,
    offerId: null,
  });
  const [imagePreview, setImagePreview] = useState<string | null>(null);

  const [formData, setFormData] = useState({
    title: '',
    subtitle: '',
    desc: '',
    active: true,
    from_date: '',
    to_date: '',
    image: null as File | null,
  });

  const fetchOffers = async () => {
    setLoading(true);
    setError(null);
    try {
      const response = await apiService.getOffers({
        active: activeFilter !== null ? activeFilter : undefined,
        page,
        size,
      });
      
      if (response.data) {
        const data = response.data as any;
        setOffers(data.items || []);
        setTotalPages(data.pages || 1);
        setTotal(data.total || 0);
      }
    } catch (err: any) {
      console.error('Offers fetch error:', err);
      setError(err.message || 'Failed to fetch offers');
      setOffers([]);
      setTotal(0);
      setTotalPages(1);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchOffers();
  }, [page, size, activeFilter]);

  const getImageUrl = (imagePath: string) => {
    if (!imagePath) return '';
    if (imagePath.startsWith('http://') || imagePath.startsWith('https://')) {
      return imagePath;
    }
    return `${API_BASE_URL}${imagePath}`;
  };

  const handleOpenDialog = (offer?: Offer) => {
    if (offer) {
      setEditingOffer(offer);
      setFormData({
        title: offer.title,
        subtitle: offer.subtitle || '',
        desc: offer.desc || '',
        active: offer.active,
        from_date: offer.from_date ? offer.from_date.split('T')[0] : '',
        to_date: offer.to_date ? offer.to_date.split('T')[0] : '',
        image: null,
      });
      setImagePreview(offer.image ? getImageUrl(offer.image) : null);
    } else {
      setEditingOffer(null);
      setFormData({
        title: '',
        subtitle: '',
        desc: '',
        active: true,
        from_date: '',
        to_date: '',
        image: null,
      });
      setImagePreview(null);
    }
    setDialogOpen(true);
  };

  const handleCloseDialog = () => {
    setDialogOpen(false);
    setEditingOffer(null);
    setImagePreview(null);
  };

  const handleImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      setFormData({ ...formData, image: file });
      // Create preview URL
      const reader = new FileReader();
      reader.onloadend = () => {
        setImagePreview(reader.result as string);
      };
      reader.readAsDataURL(file);
    }
  };

  const handleSubmit = async () => {
    try {
      const formDataToSend = new FormData();
      formDataToSend.append('title', formData.title);
      if (formData.subtitle) formDataToSend.append('subtitle', formData.subtitle);
      if (formData.desc) formDataToSend.append('desc', formData.desc);
      formDataToSend.append('active', String(formData.active));
      if (formData.from_date) formDataToSend.append('from_date', formData.from_date);
      if (formData.to_date) formDataToSend.append('to_date', formData.to_date);
      if (formData.image) formDataToSend.append('image', formData.image);

      if (editingOffer) {
        await apiService.updateOffer(editingOffer.id, formDataToSend);
        setSuccess('Offer updated successfully');
      } else {
        await apiService.createOffer(formDataToSend);
        setSuccess('Offer created successfully');
      }
      
      handleCloseDialog();
      fetchOffers();
    } catch (err: any) {
      setError(err.message || 'Failed to save offer');
    }
  };

  const handleDelete = async () => {
    if (!deleteDialog.offerId) return;
    
    try {
      await apiService.deleteOffer(deleteDialog.offerId);
      setSuccess('Offer deleted successfully');
      setDeleteDialog({ open: false, offerId: null });
      fetchOffers();
    } catch (err: any) {
      setError(err.message || 'Failed to delete offer');
    }
  };

  return (
    <Box>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
        <Typography variant="h4">Offers Management</Typography>
        <Button
          variant="contained"
          startIcon={<Add />}
          onClick={() => handleOpenDialog()}
        >
          Create Offer
        </Button>
      </Box>

      <Card sx={{ mb: 3 }}>
        <CardContent>
          <Box sx={{ display: 'flex', gap: 2, alignItems: 'center' }}>
            <FormControl size="small" sx={{ minWidth: 150 }}>
              <InputLabel>Status</InputLabel>
              <Select
                value={activeFilter === null ? '' : activeFilter}
                label="Status"
                onChange={(e) => {
                  setActiveFilter(e.target.value === '' ? null : e.target.value);
                  setPage(1);
                }}
              >
                <MenuItem value="">All</MenuItem>
                <MenuItem value={true}>Active</MenuItem>
                <MenuItem value={false}>Inactive</MenuItem>
              </Select>
            </FormControl>
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
              <Typography variant="body2" color="textSecondary">
                {total > 0 ? (
                  `Showing ${(page - 1) * size + 1} - ${Math.min(page * size, total)} of ${total}`
                ) : (
                  'No offers'
                )}
              </Typography>
              <FormControl size="small" sx={{ minWidth: 100 }}>
                <InputLabel>Per Page</InputLabel>
                <Select
                  value={size}
                  label="Per Page"
                  onChange={(e) => {
                    setSize(Number(e.target.value));
                    setPage(1);
                  }}
                >
                  <MenuItem value={10}>10</MenuItem>
                  <MenuItem value={20}>20</MenuItem>
                  <MenuItem value={50}>50</MenuItem>
                  <MenuItem value={100}>100</MenuItem>
                </Select>
              </FormControl>
            </Box>
            <IconButton onClick={fetchOffers} color="primary">
              <Refresh />
            </IconButton>
          </Box>
        </CardContent>
      </Card>

      {error && (
        <Alert severity="error" sx={{ mb: 2 }} onClose={() => setError(null)}>
          {error}
        </Alert>
      )}

      {success && (
        <Alert severity="success" sx={{ mb: 2 }} onClose={() => setSuccess(null)}>
          {success}
        </Alert>
      )}

      {loading ? (
        <Box sx={{ display: 'flex', justifyContent: 'center', p: 4 }}>
          <CircularProgress />
        </Box>
      ) : offers.length === 0 ? (
        <Card>
          <CardContent>
            <Typography color="textSecondary" align="center" sx={{ py: 4 }}>
              No offers found
            </Typography>
          </CardContent>
        </Card>
      ) : (
        <>
          <Grid container spacing={3}>
            {offers.map((offer) => {
              const imageUrl = getImageUrl(offer.image);
              return (
                <Grid item xs={12} md={6} lg={4} key={offer.id}>
                  <Card>
                    <CardContent>
                      {imageUrl && (
                        <Box sx={{ mb: 2, textAlign: 'center' }}>
                          <Box
                            component="img"
                            src={imageUrl}
                            alt={offer.title}
                            sx={{
                              width: '100%',
                              height: 200,
                              objectFit: 'cover',
                              borderRadius: 1,
                              cursor: 'pointer',
                            }}
                            onClick={() => window.open(imageUrl, '_blank')}
                          />
                        </Box>
                      )}
                      <Typography variant="h6" gutterBottom>
                        {offer.title}
                      </Typography>
                      {offer.subtitle && (
                        <Typography variant="subtitle2" color="textSecondary" gutterBottom>
                          {offer.subtitle}
                        </Typography>
                      )}
                      {offer.desc && (
                        <Typography variant="body2" color="textSecondary" sx={{ mb: 2 }}>
                          {offer.desc}
                        </Typography>
                      )}
                      {(offer.from_date || offer.to_date) && (
                        <Typography variant="caption" color="textSecondary" display="block" sx={{ mb: 1 }}>
                          {offer.from_date && offer.to_date
                            ? `${new Date(offer.from_date).toLocaleDateString()} - ${new Date(offer.to_date).toLocaleDateString()}`
                            : offer.from_date
                            ? `From: ${new Date(offer.from_date).toLocaleDateString()}`
                            : `Until: ${new Date(offer.to_date).toLocaleDateString()}`}
                        </Typography>
                      )}
                      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mt: 2 }}>
                        <Chip
                          label={offer.active ? 'Active' : 'Inactive'}
                          color={offer.active ? 'success' : 'default'}
                          size="small"
                        />
                        <Box>
                          <IconButton
                            size="small"
                            color="primary"
                            onClick={() => handleOpenDialog(offer)}
                          >
                            <Edit />
                          </IconButton>
                          <IconButton
                            size="small"
                            color="error"
                            onClick={() => setDeleteDialog({ open: true, offerId: offer.id })}
                          >
                            <Delete />
                          </IconButton>
                        </Box>
                      </Box>
                    </CardContent>
                  </Card>
                </Grid>
              );
            })}
          </Grid>

          {total > 0 && (
            <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mt: 3, flexWrap: 'wrap', gap: 2 }}>
              <Typography variant="body2" color="textSecondary">
                Page {page} of {totalPages}
              </Typography>
              <Pagination
                count={totalPages}
                page={page}
                onChange={(_, newPage) => setPage(newPage)}
                color="primary"
                showFirstButton
                showLastButton
                size="large"
              />
            </Box>
          )}
        </>
      )}

      <Dialog open={dialogOpen} onClose={handleCloseDialog} maxWidth="md" fullWidth>
        <DialogTitle>{editingOffer ? 'Edit Offer' : 'Create Offer'}</DialogTitle>
        <DialogContent>
          <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2, mt: 1 }}>
            <TextField
              label="Title"
              fullWidth
              required
              value={formData.title}
              onChange={(e) => setFormData({ ...formData, title: e.target.value })}
            />
            <TextField
              label="Subtitle"
              fullWidth
              value={formData.subtitle}
              onChange={(e) => setFormData({ ...formData, subtitle: e.target.value })}
            />
            <TextField
              label="Description"
              fullWidth
              multiline
              rows={3}
              value={formData.desc}
              onChange={(e) => setFormData({ ...formData, desc: e.target.value })}
            />
            <TextField
              label="From Date"
              type="date"
              fullWidth
              InputLabelProps={{ shrink: true }}
              value={formData.from_date}
              onChange={(e) => setFormData({ ...formData, from_date: e.target.value })}
            />
            <TextField
              label="To Date"
              type="date"
              fullWidth
              InputLabelProps={{ shrink: true }}
              value={formData.to_date}
              onChange={(e) => setFormData({ ...formData, to_date: e.target.value })}
            />
            <FormControl fullWidth>
              <InputLabel>Status</InputLabel>
              <Select
                value={formData.active}
                label="Status"
                onChange={(e) => setFormData({ ...formData, active: e.target.value as boolean })}
              >
                <MenuItem value={true}>Active</MenuItem>
                <MenuItem value={false}>Inactive</MenuItem>
              </Select>
            </FormControl>
            <Button variant="outlined" component="label">
              {formData.image ? 'Change Image' : editingOffer ? 'Update Image (Optional)' : 'Upload Image'}
              <input
                type="file"
                hidden
                accept="image/*"
                onChange={handleImageChange}
              />
            </Button>
            {imagePreview && (
              <Box sx={{ mt: 1 }}>
                <Typography variant="body2" color="textSecondary" sx={{ mb: 1 }}>
                  Image Preview:
                </Typography>
                <Box
                  component="img"
                  src={imagePreview}
                  alt="Preview"
                  sx={{
                    maxWidth: '100%',
                    maxHeight: 300,
                    objectFit: 'contain',
                    border: '1px solid',
                    borderColor: 'divider',
                    borderRadius: 1,
                  }}
                />
              </Box>
            )}
            {formData.image && !imagePreview && (
              <Typography variant="body2" color="textSecondary">
                Selected: {formData.image.name}
              </Typography>
            )}
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseDialog}>Cancel</Button>
          <Button onClick={handleSubmit} variant="contained">
            {editingOffer ? 'Update' : 'Create'}
          </Button>
        </DialogActions>
      </Dialog>

      <Dialog open={deleteDialog.open} onClose={() => setDeleteDialog({ open: false, offerId: null })}>
        <DialogTitle>Delete Offer</DialogTitle>
        <DialogContent>
          <Typography>Are you sure you want to delete this offer? This action cannot be undone.</Typography>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setDeleteDialog({ open: false, offerId: null })}>Cancel</Button>
          <Button onClick={handleDelete} variant="contained" color="error">
            Delete
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default OffersPage;

