import React, { useState, useEffect } from 'react';
import {
  Box,
  Card,
  CardContent,
  Typography,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  TextField,
  Button,
  Chip,
  IconButton,
  MenuItem,
  Select,
  FormControl,
  InputLabel,
  Pagination,
  CircularProgress,
  Alert,
  Menu,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  DialogContentText,
} from '@mui/material';
import { Search, Refresh, CheckCircle, Cancel, MoreVert, Add, Remove } from '@mui/icons-material';
import { apiService } from '../../services/api';

interface Wallet {
  wallet_id: number;
  user_id: string;
  username: string;
  email: string;
  main_wallet: string;
  reserve_wallet: string;
  worth: string;
  active: boolean;
}

const WalletsPage: React.FC = () => {
  const [wallets, setWallets] = useState<Wallet[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [userId, setUserId] = useState('');
  const [searchTerm, setSearchTerm] = useState('');
  const [activeFilter, setActiveFilter] = useState<boolean | null>(null);
  const [page, setPage] = useState(1);
  const [size, setSize] = useState(20);
  const [totalPages, setTotalPages] = useState(1);
  const [total, setTotal] = useState(0);
  const [success, setSuccess] = useState<string | null>(null);
  const [updatingWalletId, setUpdatingWalletId] = useState<number | null>(null);
  const [menuAnchor, setMenuAnchor] = useState<{ el: HTMLElement; wallet: Wallet } | null>(null);
  const [creditDebitDialog, setCreditDebitDialog] = useState<{
    open: boolean;
    wallet: Wallet | null;
    type: 'credit' | 'debit' | null;
  }>({ open: false, wallet: null, type: null });
  const [amount, setAmount] = useState('');
  const [remark, setRemark] = useState('');
  const [processing, setProcessing] = useState(false);

  const fetchWallets = async () => {
    setLoading(true);
    setError(null);
    try {
      const response = await apiService.getWallets({
        user_id: userId || undefined,
        search: searchTerm || undefined,
        active: activeFilter !== null ? activeFilter : undefined,
        page,
        size,
      });
      
      if (response.data) {
        const data = response.data as any;
        setWallets(data.items || []);
        setTotalPages(data.pages || data.total_pages || 1);
        setTotal(data.total || 0);
      }
    } catch (err: any) {
      console.error('Wallets fetch error:', err);
      setError(err.message || 'Failed to fetch wallets');
      setWallets([]);
      setTotal(0);
      setTotalPages(1);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchWallets();
  }, [page, size]);

  const handleSearch = () => {
    setPage(1);
    fetchWallets();
  };

  const handleRefresh = () => {
    fetchWallets();
  };

  const handleToggleWalletStatus = async (walletId: number, currentStatus: boolean) => {
    setUpdatingWalletId(walletId);
    setError(null);
    setSuccess(null);
    
    try {
      const response = await apiService.updateWalletStatus(walletId, !currentStatus);
      
      if (response.data) {
        const updatedWallet = response.data as any;
        
        // Update the wallet in the local state
        setWallets((prevWallets: Wallet[]) => 
          prevWallets.map((wallet: Wallet) => 
            wallet.wallet_id === walletId
              ? {
                  ...wallet,
                  active: updatedWallet.new_status,
                  main_wallet: updatedWallet.main_wallet || wallet.main_wallet,
                  reserve_wallet: updatedWallet.reserve_wallet || wallet.reserve_wallet,
                  worth: updatedWallet.worth || wallet.worth,
                }
              : wallet
          )
        );
        
        setSuccess(response.msg || `Wallet ${updatedWallet.new_status ? 'activated' : 'deactivated'} successfully`);
      }
    } catch (err: any) {
      setError(err.message || `Failed to ${!currentStatus ? 'activate' : 'deactivate'} wallet`);
    } finally {
      setUpdatingWalletId(null);
    }
  };

  const handleMenuOpen = (event: React.MouseEvent<HTMLElement>, wallet: Wallet) => {
    setMenuAnchor({ el: event.currentTarget, wallet });
  };

  const handleMenuClose = () => {
    setMenuAnchor(null);
  };

  const handleCreditDebitOpen = (type: 'credit' | 'debit', wallet: Wallet) => {
    setCreditDebitDialog({ open: true, wallet, type });
    setAmount('');
    setRemark('');
    handleMenuClose();
  };

  const handleCreditDebitClose = () => {
    setCreditDebitDialog({ open: false, wallet: null, type: null });
    setAmount('');
    setRemark('');
  };

  const handleCreditDebitSubmit = async () => {
    if (!creditDebitDialog.wallet || !creditDebitDialog.type) return;

    const amountNum = parseFloat(amount);
    if (!amount || isNaN(amountNum) || amountNum <= 0) {
      setError('Please enter a valid amount');
      return;
    }

    setProcessing(true);
    setError(null);
    setSuccess(null);

    try {
      const response = creditDebitDialog.type === 'credit'
        ? await apiService.creditWallet(
            creditDebitDialog.wallet.user_id,
            amount,
            'main',
            remark || undefined
          )
        : await apiService.debitWallet(
            creditDebitDialog.wallet.user_id,
            amount,
            'main',
            remark || undefined
          );

      if (response.data) {
        const data = response.data as any;
        
        // Update the wallet in the local state
        setWallets((prevWallets: Wallet[]) => 
          prevWallets.map((wallet: Wallet) => {
            if (wallet.user_id === creditDebitDialog.wallet!.user_id) {
              const updatedMainWallet = data.transaction?.closing_balance || wallet.main_wallet;
              
              return {
                ...wallet,
                main_wallet: updatedMainWallet,
                worth: String(
                  parseFloat(updatedMainWallet || '0') + parseFloat(wallet.reserve_wallet || '0')
                ),
              };
            }
            return wallet;
          })
        );

        setSuccess(response.msg || `Successfully ${creditDebitDialog.type === 'credit' ? 'credited' : 'debited'} ${amount} to main wallet`);
        handleCreditDebitClose();
      }
    } catch (err: any) {
      setError(err.message || `Failed to ${creditDebitDialog.type} wallet`);
    } finally {
      setProcessing(false);
    }
  };

  return (
    <Box>
      <Typography variant="h4" gutterBottom>
        Wallets Management
      </Typography>

      <Card sx={{ mb: 3 }}>
        <CardContent>
          <Box sx={{ display: 'flex', gap: 2, flexWrap: 'wrap', alignItems: 'center' }}>
            <TextField
              label="User ID"
              variant="outlined"
              size="small"
              value={userId}
              onChange={(e: { target: { value: any; }; }) => setUserId(e.target.value)}
              sx={{ minWidth: 200 }}
            />
            <TextField
              label="Search (Email, Username, Phone)"
              variant="outlined"
              size="small"
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              onKeyDown={(e) => {
                if (e.key === 'Enter') {
                  handleSearch();
                }
              }}
              sx={{ minWidth: 250 }}
              placeholder="Search by email, username, or phone"
            />
            <FormControl size="small" sx={{ minWidth: 150 }}>
              <InputLabel>Status</InputLabel>
              <Select
                value={activeFilter === null ? '' : String(activeFilter)}
                label="Status"
                onChange={(e) =>
                  setActiveFilter(
                    e.target.value === ''
                      ? null
                      : e.target.value === 'true'
                      ? true
                      : false
                  )
                }
              >
                <MenuItem value="">All</MenuItem>
                <MenuItem value="true">Active</MenuItem>
                <MenuItem value="false">Inactive</MenuItem>
              </Select>
            </FormControl>
            <Button
              variant="contained"
              startIcon={<Search />}
              onClick={handleSearch}
            >
              Search
            </Button>
            <IconButton onClick={handleRefresh} color="primary">
              <Refresh />
            </IconButton>
          </Box>
        </CardContent>
      </Card>

      {error && (
        <Alert severity="error" sx={{ mb: 2 }} onClose={() => setError(null)}>
          {error}
        </Alert>
      )}

      {success && (
        <Alert severity="success" sx={{ mb: 2 }} onClose={() => setSuccess(null)}>
          {success}
        </Alert>
      )}

      <Card>
        <CardContent>
          <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2, flexWrap: 'wrap', gap: 2 }}>
            <Typography variant="h6">
              Wallets ({total})
            </Typography>
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
              <Typography variant="body2" color="textSecondary">
                {total > 0 ? (
                  `Showing ${(page - 1) * size + 1} - ${Math.min(page * size, total)} of ${total}`
                ) : (
                  'No wallets'
                )}
              </Typography>
              <FormControl size="small" sx={{ minWidth: 100 }}>
                <InputLabel>Per Page</InputLabel>
                <Select
                  value={size}
                  label="Per Page"
                  onChange={(e) => {
                    setSize(Number(e.target.value));
                    setPage(1);
                  }}
                >
                  <MenuItem value={10}>10</MenuItem>
                  <MenuItem value={20}>20</MenuItem>
                  <MenuItem value={50}>50</MenuItem>
                  <MenuItem value={100}>100</MenuItem>
                </Select>
              </FormControl>
            </Box>
          </Box>

          {loading ? (
            <Box sx={{ display: 'flex', justifyContent: 'center', p: 4 }}>
              <CircularProgress />
            </Box>
          ) : (
            <>
              <TableContainer component={Paper} variant="outlined">
                <Table>
                  <TableHead>
                    <TableRow>
                      <TableCell>Wallet ID</TableCell>
                      <TableCell>Username</TableCell>
                      <TableCell>Email</TableCell>
                      <TableCell>User ID</TableCell>
                      <TableCell>Main Wallet</TableCell>
                      <TableCell>Reserve Wallet</TableCell>
                      <TableCell>Worth</TableCell>
                      <TableCell>Status</TableCell>
                      <TableCell>Actions</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {wallets.length === 0 ? (
                      <TableRow>
                        <TableCell colSpan={9} align="center">
                          <Typography color="textSecondary">No wallets found</Typography>
                        </TableCell>
                      </TableRow>
                    ) : (
                      wallets.map((wallet) => (
                        <TableRow key={wallet.wallet_id} hover>
                          <TableCell>{wallet.wallet_id}</TableCell>
                          <TableCell>
                            <Typography variant="body2" fontWeight="medium">
                              {wallet.username}
                            </Typography>
                          </TableCell>
                          <TableCell>{wallet.email}</TableCell>
                          <TableCell>
                            <Typography variant="body2" sx={{ fontFamily: 'monospace', fontSize: '0.75rem' }}>
                              {wallet.user_id && wallet.user_id.length > 8 ? (
                                <>
                                  {wallet.user_id.substring(0, 8)}...
                                </>
                              ) : (
                                wallet.user_id
                              )}
                            </Typography>
                          </TableCell>
                          <TableCell>
                            <Typography variant="body2" fontWeight="bold" color="primary.main">
                              
                              {Number.isNaN(parseFloat(wallet.main_wallet))
                                ? '0.00'
                                : parseFloat(wallet.main_wallet || '0').toFixed(2)}
                            </Typography>
                          </TableCell>
                          <TableCell>
                            <Typography variant="body2" fontWeight="bold" color="secondary.main">
                              
                              {Number.isNaN(parseFloat(wallet.reserve_wallet))
                                ? '0.00'
                                : parseFloat(wallet.reserve_wallet || '0').toFixed(2)}
                            </Typography>
                          </TableCell>
                          <TableCell>
                            <Typography variant="body2" fontWeight="bold" color="success.main">
                              
                              {Number.isNaN(parseFloat(wallet.worth))
                                ? '0.00'
                                : parseFloat(wallet.worth || '0').toFixed(2)}
                            </Typography>
                          </TableCell>
                          <TableCell>
                            <Chip
                              label={wallet.active ? 'Active' : 'Inactive'}
                              color={wallet.active ? 'success' : 'default'}
                              size="small"
                            />
                          </TableCell>
                          <TableCell>
                            <Box sx={{ display: 'flex', gap: 0.5, alignItems: 'center' }}>
                              <IconButton
                                size="small"
                                color={wallet.active ? 'error' : 'success'}
                                onClick={() => handleToggleWalletStatus(wallet.wallet_id, wallet.active)}
                                disabled={updatingWalletId === wallet.wallet_id}
                                title={wallet.active ? 'Deactivate wallet' : 'Activate wallet'}
                              >
                                {updatingWalletId === wallet.wallet_id ? (
                                  <CircularProgress size={20} />
                                ) : wallet.active ? (
                                  <Cancel />
                                ) : (
                                  <CheckCircle />
                                )}
                              </IconButton>
                              <IconButton
                                size="medium"
                                onClick={(e) => handleMenuOpen(e, wallet)}
                                title="More options"
                                sx={{ 
                                  color: 'text.secondary',
                                  '&:hover': {
                                    backgroundColor: 'action.hover',
                                    color: 'text.primary'
                                  }
                                }}
                              >
                                <MoreVert fontSize="medium" />
                              </IconButton>
                            </Box>
                          </TableCell>
                        </TableRow>
                      ))
                    )}
                  </TableBody>
                </Table>
              </TableContainer>

              {total > 0 && (
                <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mt: 3, flexWrap: 'wrap', gap: 2 }}>
                  <Typography variant="body2" color="textSecondary">
                    Page {page} of {totalPages}
                  </Typography>
                  <Pagination
                    count={totalPages}
                    page={page}
                    onChange={(_, newPage) => setPage(newPage)}
                    color="primary"
                    showFirstButton
                    showLastButton
                    size="large"
                  />
                </Box>
              )}
            </>
          )}
        </CardContent>
      </Card>

      {/* Wallet Actions Menu */}
      <Menu
        anchorEl={menuAnchor?.el}
        open={Boolean(menuAnchor)}
        onClose={handleMenuClose}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'right',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
      >
        <MenuItem
          onClick={() => menuAnchor && handleCreditDebitOpen('credit', menuAnchor.wallet)}
          disabled={!menuAnchor?.wallet?.active}
        >
          <Add sx={{ mr: 1, fontSize: 18 }} />
          Credit Wallet
        </MenuItem>
        <MenuItem
          onClick={() => menuAnchor && handleCreditDebitOpen('debit', menuAnchor.wallet)}
          disabled={!menuAnchor?.wallet?.active}
        >
          <Remove sx={{ mr: 1, fontSize: 18 }} />
          Debit Wallet
        </MenuItem>
      </Menu>

      {/* Credit/Debit Dialog */}
      <Dialog
        open={creditDebitDialog.open}
        onClose={handleCreditDebitClose}
        maxWidth="sm"
        fullWidth
      >
        <DialogTitle>
          {creditDebitDialog.type === 'credit' ? ' Credit Wallet' : ' Debit Wallet'}
        </DialogTitle>
        <DialogContent>
          <DialogContentText sx={{ mb: 2 }}>
            {creditDebitDialog.wallet && (
              <>
                <strong>User:</strong> {creditDebitDialog.wallet.username} ({creditDebitDialog.wallet.email})
                <br />
                <strong>Current Main Wallet:</strong> 
                {Number.isNaN(parseFloat(creditDebitDialog.wallet.main_wallet))
                  ? '0.00'
                  : parseFloat(creditDebitDialog.wallet.main_wallet || '0').toFixed(2)}
                <br />
                <strong>Current Reserve Wallet:</strong> 
                {Number.isNaN(parseFloat(creditDebitDialog.wallet.reserve_wallet))
                  ? '0.00'
                  : parseFloat(creditDebitDialog.wallet.reserve_wallet || '0').toFixed(2)}
              </>
            )}
          </DialogContentText>

          <TextField
            fullWidth
            label="Amount"
            type="number"
            value={amount}
            onChange={(e) => setAmount(e.target.value)}
            margin="normal"
            required
            inputProps={{ min: 0, step: 0.01 }}
            helperText={`Enter amount to ${creditDebitDialog.type === 'credit' ? 'credit' : 'debit'}`}
            sx={{ mb: 2 }}
            error={!!amount && (isNaN(Number(amount)) || Number(amount) <= 0)}
          />

          <TextField
            fullWidth
            label="Remark (Optional)"
            multiline
            rows={3}
            value={remark}
            onChange={(e) => setRemark(e.target.value)}
            margin="normal"
            placeholder="e.g., Refund for order #123"
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCreditDebitClose} disabled={processing}>
            Cancel
          </Button>
          <Button
            onClick={handleCreditDebitSubmit}
            variant="contained"
            color={creditDebitDialog.type === 'credit' ? 'success' : 'error'}
            disabled={
              processing ||
              !amount ||
              isNaN(Number(amount)) ||
              Number(amount) <= 0
            }
            startIcon={processing ? <CircularProgress size={16} /> : creditDebitDialog.type === 'credit' ? <Add /> : <Remove />}
          >
            {processing ? 'Processing...' : creditDebitDialog.type === 'credit' ? 'Credit' : 'Debit'}
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default WalletsPage;

