import React, { useState, useEffect } from 'react';
import {
  Box,
  Card,
  CardContent,
  Typography,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  TextField,
  Button,
  Chip,
  IconButton,
  MenuItem,
  Select,
  FormControl,
  InputLabel,
  Pagination,
  CircularProgress,
  Alert,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  DialogContentText,
} from '@mui/material';
import { Search, Refresh, Edit } from '@mui/icons-material';
import { apiService } from '../../services/api';

interface RechargeTransaction {
  id: number;
  txnid: string;
  user_id: string;
  ref_txnid: string;
  opid: string;
  operator_name?: string;
  mobile: string;
  amount: string;
  commission: string;
  status: string;
  provider_id: number;
  remark: string;
  created_at: string;
}

const RechargeTransactionsPage: React.FC = () => {
  const [transactions, setTransactions] = useState<RechargeTransaction[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [success, setSuccess] = useState<string | null>(null);
  const [userId, setUserId] = useState('');
  const [status, setStatus] = useState('');
  const [mobile, setMobile] = useState('');
  const [refTxnid, setRefTxnid] = useState('');
  const [fromDate, setFromDate] = useState('');
  const [toDate, setToDate] = useState('');
  const [page, setPage] = useState(1);
  const [size, setSize] = useState(20);
  const [totalPages, setTotalPages] = useState(1);
  const [total, setTotal] = useState(0);
  const [updateDialog, setUpdateDialog] = useState<{ open: boolean; txn: RechargeTransaction | null }>({
    open: false,
    txn: null,
  });
  const [newStatus, setNewStatus] = useState<'success' | 'failed'>('success');
  const [remark, setRemark] = useState('');

  const fetchTransactions = async () => {
    setLoading(true);
    setError(null);
    try {
      const response = await apiService.getRechargeTransactions({
        user_id: userId || undefined,
        status: status || undefined,
        mobile: mobile || undefined,
        ref_txnid: refTxnid || undefined,
        from_date: fromDate || undefined,
        to_date: toDate || undefined,
        page,
        size,
      });
      
      if (response.data) {
        const data = response.data as any;
        setTransactions(data.items || []);
        setTotalPages(data.pages || data.total_pages || 1);
        setTotal(data.total || 0);
      }
    } catch (err: any) {
      console.error('Recharge transactions fetch error:', err);
      setError(err.message || 'Failed to fetch transactions');
      setTransactions([]);
      setTotal(0);
      setTotalPages(1);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchTransactions();
  }, [page, size]);

  const handleSearch = () => {
    setPage(1);
    fetchTransactions();
  };

  const handleRefresh = () => {
    fetchTransactions();
  };

  const handleUpdateStatus = async () => {
    if (!updateDialog.txn) return;
    
    try {
      await apiService.updateTransactionStatus(updateDialog.txn.txnid, newStatus, remark || undefined);
      setSuccess('Transaction status updated successfully');
      setUpdateDialog({ open: false, txn: null });
      setRemark('');
      fetchTransactions();
    } catch (err: any) {
      setError(err.message || 'Failed to update transaction status');
    }
  };

  const getStatusColor = (status: string) => {
    switch (status.toLowerCase()) {
      case 'success':
        return 'success';
      case 'pending':
        return 'warning';
      case 'failed':
        return 'error';
      default:
        return 'default';
    }
  };

  return (
    <Box>
      <Typography variant="h4" gutterBottom>
        Recharge Transactions
      </Typography>

      <Card sx={{ mb: 3 }}>
        <CardContent>
          <Box sx={{ display: 'flex', gap: 2, flexWrap: 'wrap', alignItems: 'center' }}>
            <TextField
              label="User ID"
              variant="outlined"
              size="small"
              value={userId}
              onChange={(e) => setUserId(e.target.value)}
              sx={{ minWidth: 200 }}
            />
            <FormControl size="small" sx={{ minWidth: 150 }}>
              <InputLabel>Status</InputLabel>
              <Select
                value={status}
                label="Status"
                onChange={(e) => setStatus(e.target.value)}
              >
                <MenuItem value="">All</MenuItem>
                <MenuItem value="success">Success</MenuItem>
                <MenuItem value="pending">Pending</MenuItem>
                <MenuItem value="failed">Failed</MenuItem>
              </Select>
            </FormControl>
            <TextField
              label="Mobile"
              variant="outlined"
              size="small"
              value={mobile}
              onChange={(e) => setMobile(e.target.value)}
              sx={{ minWidth: 150 }}
            />
            <TextField
              label="Ref Txn ID"
              variant="outlined"
              size="small"
              value={refTxnid}
              onChange={(e) => setRefTxnid(e.target.value)}
              sx={{ minWidth: 150 }}
            />
            <TextField
              label="From Date"
              type="date"
              variant="outlined"
              size="small"
              value={fromDate}
              onChange={(e) => setFromDate(e.target.value)}
              InputLabelProps={{ shrink: true }}
              sx={{ minWidth: 150 }}
            />
            <TextField
              label="To Date"
              type="date"
              variant="outlined"
              size="small"
              value={toDate}
              onChange={(e) => setToDate(e.target.value)}
              InputLabelProps={{ shrink: true }}
              sx={{ minWidth: 150 }}
            />
            <Button
              variant="contained"
              startIcon={<Search />}
              onClick={handleSearch}
            >
              Search
            </Button>
            <IconButton onClick={handleRefresh} color="primary">
              <Refresh />
            </IconButton>
          </Box>
        </CardContent>
      </Card>

      {error && (
        <Alert severity="error" sx={{ mb: 2 }} onClose={() => setError(null)}>
          {error}
        </Alert>
      )}

      {success && (
        <Alert severity="success" sx={{ mb: 2 }} onClose={() => setSuccess(null)}>
          {success}
        </Alert>
      )}

      <Card>
        <CardContent>
          <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2, flexWrap: 'wrap', gap: 2 }}>
            <Typography variant="h6">
              Transactions ({total})
            </Typography>
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
              <Typography variant="body2" color="textSecondary">
                {total > 0 ? (
                  `Showing ${(page - 1) * size + 1} - ${Math.min(page * size, total)} of ${total}`
                ) : (
                  'No transactions'
                )}
              </Typography>
              <FormControl size="small" sx={{ minWidth: 100 }}>
                <InputLabel>Per Page</InputLabel>
                <Select
                  value={size}
                  label="Per Page"
                  onChange={(e) => {
                    setSize(Number(e.target.value));
                    setPage(1);
                  }}
                >
                  <MenuItem value={10}>10</MenuItem>
                  <MenuItem value={20}>20</MenuItem>
                  <MenuItem value={50}>50</MenuItem>
                  <MenuItem value={100}>100</MenuItem>
                </Select>
              </FormControl>
            </Box>
          </Box>

          {loading ? (
            <Box sx={{ display: 'flex', justifyContent: 'center', p: 4 }}>
              <CircularProgress />
            </Box>
          ) : (
            <>
              <TableContainer component={Paper} variant="outlined">
                <Table>
                  <TableHead>
                    <TableRow>
                      <TableCell>Txn ID</TableCell>
                      <TableCell>User ID</TableCell>
                      <TableCell>Mobile</TableCell>
                      <TableCell>Operator</TableCell>
                      <TableCell>Amount</TableCell>
                      <TableCell>Commission</TableCell>
                      <TableCell>Status</TableCell>
                      <TableCell>Ref Txn ID</TableCell>
                      <TableCell>Created At</TableCell>
                      <TableCell>Actions</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {transactions.length === 0 ? (
                      <TableRow>
                        <TableCell colSpan={10} align="center">
                          <Typography color="textSecondary">No transactions found</Typography>
                        </TableCell>
                      </TableRow>
                    ) : (
                      transactions.map((txn) => (
                        <TableRow key={txn.id} hover>
                          <TableCell>
                            <Typography variant="body2" sx={{ fontFamily: 'monospace', fontSize: '0.75rem' }}>
                              {txn.txnid}
                            </Typography>
                          </TableCell>
                          <TableCell>
                            <Typography variant="body2" sx={{ fontFamily: 'monospace', fontSize: '0.75rem' }}>
                              {txn.user_id.substring(0, 8)}...
                            </Typography>
                          </TableCell>
                          <TableCell>{txn.mobile}</TableCell>
                          <TableCell>
                            <Chip label={txn.operator_name || txn.opid} size="small" variant="outlined" />
                          </TableCell>
                          <TableCell>
                            <Typography variant="body2" fontWeight="bold">
                              {parseFloat(txn.amount || '0').toFixed(2)}
                            </Typography>
                          </TableCell>
                          <TableCell>
                            <Typography variant="body2" color="textSecondary">
                              {parseFloat(txn.commission || '0').toFixed(2)}
                            </Typography>
                          </TableCell>
                          <TableCell>
                            <Chip
                              label={txn.status}
                              color={getStatusColor(txn.status) as any}
                              size="small"
                            />
                          </TableCell>
                          <TableCell>
                            <Typography variant="body2" sx={{ fontFamily: 'monospace', fontSize: '0.75rem' }}>
                              {txn.ref_txnid}
                            </Typography>
                          </TableCell>
                          <TableCell>
                            {new Date(txn.created_at).toLocaleString()}
                          </TableCell>
                          <TableCell>
                            {txn.status === 'pending' && (
                              <IconButton
                                size="small"
                                color="primary"
                                onClick={() => setUpdateDialog({ open: true, txn })}
                              >
                                <Edit />
                              </IconButton>
                            )}
                          </TableCell>
                        </TableRow>
                      ))
                    )}
                  </TableBody>
                </Table>
              </TableContainer>

              {total > 0 && (
                <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mt: 3, flexWrap: 'wrap', gap: 2 }}>
                  <Typography variant="body2" color="textSecondary">
                    Page {page} of {totalPages}
                  </Typography>
                  <Pagination
                    count={totalPages}
                    page={page}
                    onChange={(_, newPage) => setPage(newPage)}
                    color="primary"
                    showFirstButton
                    showLastButton
                    size="large"
                  />
                </Box>
              )}
            </>
          )}
        </CardContent>
      </Card>

      <Dialog open={updateDialog.open} onClose={() => setUpdateDialog({ open: false, txn: null })}>
        <DialogTitle>Update Transaction Status</DialogTitle>
        <DialogContent>
          <DialogContentText sx={{ mb: 2 }}>
            Update status for transaction: {updateDialog.txn?.txnid}
          </DialogContentText>
          <FormControl fullWidth sx={{ mb: 2 }}>
            <InputLabel>New Status</InputLabel>
            <Select
              value={newStatus}
              label="New Status"
              onChange={(e) => setNewStatus(e.target.value as 'success' | 'failed')}
            >
              <MenuItem value="success">Success</MenuItem>
              <MenuItem value="failed">Failed</MenuItem>
            </Select>
          </FormControl>
          <TextField
            fullWidth
            label="Remark (Optional)"
            multiline
            rows={3}
            value={remark}
            onChange={(e) => setRemark(e.target.value)}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setUpdateDialog({ open: false, txn: null })}>Cancel</Button>
          <Button onClick={handleUpdateStatus} variant="contained" color="primary">
            Update
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default RechargeTransactionsPage;

