import React, { useState, useEffect } from 'react';
import {
  Box,
  Card,
  CardContent,
  Typography,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  TextField,
  Button,
  Chip,
  IconButton,
  MenuItem,
  Select,
  FormControl,
  InputLabel,
  Pagination,
  CircularProgress,
  Alert,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Tooltip,
} from '@mui/material';
import { Search, Refresh, CheckCircle, Cancel } from '@mui/icons-material';
import { apiService } from '../../services/api';

interface User {
  id: string;
  username: string;
  email?: string;
  phone: string;
  first_name?: string;
  last_name?: string;
  role: string;
  package: string;
  package_id?: number;
  active: boolean;
  created_at: string;
  updated_at: string | null;
}

interface Package {
  id: number;
  name: string;
  active: boolean;
}

const UsersPage: React.FC = () => {
  const [users, setUsers] = useState<User[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [search, setSearch] = useState('');
  const [activeFilter, setActiveFilter] = useState<boolean | null>(null);
  const [page, setPage] = useState(1);
  const [size, setSize] = useState(20);
  const [totalPages, setTotalPages] = useState(1);
  const [total, setTotal] = useState(0);
  const [success, setSuccess] = useState<string | null>(null);
  const [updatingUserId, setUpdatingUserId] = useState<string | null>(null);
  const [packageDialog, setPackageDialog] = useState<{ open: boolean; userId: string | null; userName: string }>({
    open: false,
    userId: null,
    userName: '',
  });
  const [packages, setPackages] = useState<Package[]>([]);
  const [packagesLoading, setPackagesLoading] = useState(false);
  const [selectedPackageId, setSelectedPackageId] = useState<string>('');
  const [updatingPackage, setUpdatingPackage] = useState(false);

  const fetchUsers = async () => {
    setLoading(true);
    setError(null);
    try {
      const response = await apiService.getUsers({
        search: search || undefined,
        active: activeFilter !== null ? activeFilter : undefined,
        page,
        size,
      });
      
      if (response.data) {
        const data = response.data as any;
        setUsers(data.items || []);
        setTotalPages(data.pages || 1);
        setTotal(data.total || 0);
      }
    } catch (err: any) {
      setError(err.message || 'Failed to fetch users');
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchUsers();
  }, [page, size]);

  const handleSearch = () => {
    setPage(1);
    fetchUsers();
  };

  const handleRefresh = () => {
    fetchUsers();
  };

  const handleToggleUserStatus = async (userId: string, currentStatus: boolean) => {
    setUpdatingUserId(userId);
    setError(null);
    setSuccess(null);
    
    try {
      const response = await apiService.updateUserStatus(userId, !currentStatus);
      
      if (response.data) {
        // Update the user in the local state
        setUsers((prevUsers: User[]) => 
          prevUsers.map((user: User) => 
            user.id === userId
              ? { ...user, active: !currentStatus }
              : user
          )
        );
        
        setSuccess(response.msg || `User ${!currentStatus ? 'activated' : 'deactivated'} successfully`);
      }
    } catch (err: any) {
      setError(err.message || `Failed to ${!currentStatus ? 'activate' : 'deactivate'} user`);
    } finally {
      setUpdatingUserId(null);
    }
  };

  const handleOpenPackageDialog = async (userId: string, userName: string) => {
    setPackageDialog({ open: true, userId, userName });
    setSelectedPackageId('');
    setError(null);
    setSuccess(null);
    
    // Fetch packages
    setPackagesLoading(true);
    try {
      const response = await apiService.getPackages();
      if (response.data) {
        const data = response.data as any;
        setPackages(data.packages || []);
      }
    } catch (err: any) {
      setError(err.message || 'Failed to fetch packages');
    } finally {
      setPackagesLoading(false);
    }
  };

  const handleClosePackageDialog = () => {
    setPackageDialog({ open: false, userId: null, userName: '' });
    setSelectedPackageId('');
  };

  const handleUpdatePackage = async () => {
    if (!packageDialog.userId || !selectedPackageId) {
      setError('Please select a package');
      return;
    }

    setUpdatingPackage(true);
    setError(null);
    setSuccess(null);

    try {
      const response = await apiService.updateUserPackage(packageDialog.userId, parseInt(selectedPackageId));
      
      if (response.data) {
        // Update the user in the local state
        const selectedPackage = packages.find(p => p.id === parseInt(selectedPackageId));
        setUsers((prevUsers: User[]) => 
          prevUsers.map((user: User) => 
            user.id === packageDialog.userId
              ? { ...user, package: selectedPackage?.name || user.package, package_id: parseInt(selectedPackageId) }
              : user
          )
        );
        
        setSuccess(response.msg || 'User package updated successfully');
        handleClosePackageDialog();
      }
    } catch (err: any) {
      setError(err.message || 'Failed to update user package');
    } finally {
      setUpdatingPackage(false);
    }
  };

  return (
    <Box>
      <Typography variant="h4" gutterBottom>
        Users Management
      </Typography>

      <Card sx={{ mb: 3 }}>
        <CardContent>
          <Box sx={{ display: 'flex', gap: 2, flexWrap: 'wrap', alignItems: 'center' }}>
            <TextField
              label="Search"
              variant="outlined"
              size="small"
              value={search}
              onChange={(e) => setSearch(e.target.value)}
              onKeyPress={(e) => e.key === 'Enter' && handleSearch()}
              sx={{ flexGrow: 1, minWidth: 200 }}
            />
            <FormControl size="small" sx={{ minWidth: 150 }}>
              <InputLabel>Status</InputLabel>
              <Select
                value={activeFilter === null ? '' : activeFilter}
                label="Status"
                onChange={(e) => setActiveFilter(e.target.value === '' ? null : e.target.value)}
              >
                <MenuItem value="">All</MenuItem>
                <MenuItem value={true}>Active</MenuItem>
                <MenuItem value={false}>Inactive</MenuItem>
              </Select>
            </FormControl>
            <Button
              variant="contained"
              startIcon={<Search />}
              onClick={handleSearch}
            >
              Search
            </Button>
            <IconButton onClick={handleRefresh} color="primary">
              <Refresh />
            </IconButton>
          </Box>
        </CardContent>
      </Card>

      {error && (
        <Alert severity="error" sx={{ mb: 2 }} onClose={() => setError(null)}>
          {error}
        </Alert>
      )}

      {success && (
        <Alert severity="success" sx={{ mb: 2 }} onClose={() => setSuccess(null)}>
          {success}
        </Alert>
      )}

      <Card>
        <CardContent>
          <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2, flexWrap: 'wrap', gap: 2 }}>
            <Typography variant="h6">
              Users ({total})
            </Typography>
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
              <Typography variant="body2" color="textSecondary">
                {total > 0 ? (
                  `Showing ${(page - 1) * size + 1} - ${Math.min(page * size, total)} of ${total}`
                ) : (
                  'No users'
                )}
              </Typography>
              <FormControl size="small" sx={{ minWidth: 100 }}>
                <InputLabel>Per Page</InputLabel>
                <Select
                  value={size}
                  label="Per Page"
                  onChange={(e) => {
                    setSize(Number(e.target.value));
                    setPage(1);
                  }}
                >
                  <MenuItem value={10}>10</MenuItem>
                  <MenuItem value={20}>20</MenuItem>
                  <MenuItem value={50}>50</MenuItem>
                  <MenuItem value={100}>100</MenuItem>
                </Select>
              </FormControl>
            </Box>
          </Box>

          {loading ? (
            <Box sx={{ display: 'flex', justifyContent: 'center', p: 4 }}>
              <CircularProgress />
            </Box>
          ) : (
            <>
              <TableContainer component={Paper} variant="outlined">
                <Table>
                  <TableHead>
                    <TableRow>
                      <TableCell>Username</TableCell>
                      <TableCell>Name</TableCell>
                      <TableCell>Email</TableCell>
                      <TableCell>Phone</TableCell>
                      <TableCell>Role</TableCell>
                      <TableCell>Package</TableCell>
                      <TableCell>Status</TableCell>
                      <TableCell>Created At</TableCell>
                      <TableCell>Actions</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {users.length === 0 ? (
                      <TableRow>
                        <TableCell colSpan={9} align="center">
                          <Typography color="textSecondary">No users found</Typography>
                        </TableCell>
                      </TableRow>
                    ) : (
                      users.map((user) => (
                        <TableRow key={user.id} hover>
                          <TableCell>
                            <Typography variant="body2" fontWeight="medium">
                              {user.username}
                            </Typography>
                          </TableCell>
                          <TableCell>
                            {user.first_name || user.last_name
                              ? `${user.first_name || ''} ${user.last_name || ''}`.trim()
                              : 'N/A'}
                          </TableCell>
                          <TableCell>{user.email || 'N/A'}</TableCell>
                          <TableCell>{user.phone}</TableCell>
                          <TableCell>
                            <Chip label={user.role} size="small" variant="outlined" />
                          </TableCell>
                          <TableCell>
                            <Chip 
                              label={user.package} 
                              size="small" 
                              color="primary" 
                              variant="outlined"
                              onClick={() => handleOpenPackageDialog(user.id, user.username)}
                              sx={{ cursor: 'pointer', '&:hover': { opacity: 0.8 } }}
                              title="Click to change package"
                            />
                          </TableCell>
                          <TableCell>
                            <Chip
                              label={user.active ? 'Active' : 'Inactive'}
                              color={user.active ? 'success' : 'default'}
                              size="small"
                            />
                          </TableCell>
                          <TableCell>
                            {new Date(user.created_at).toLocaleString()}
                          </TableCell>
                          <TableCell>
                            <Tooltip title={user.active ? 'Deactivate user' : 'Activate user'}>
                              <IconButton
                                size="small"
                                color={user.active ? 'error' : 'success'}
                                onClick={() => handleToggleUserStatus(user.id, user.active)}
                                disabled={updatingUserId === user.id}
                              >
                                {updatingUserId === user.id ? (
                                  <CircularProgress size={20} />
                                ) : user.active ? (
                                  <Cancel />
                                ) : (
                                  <CheckCircle />
                                )}
                              </IconButton>
                            </Tooltip>
                          </TableCell>
                        </TableRow>
                      ))
                    )}
                  </TableBody>
                </Table>
              </TableContainer>

              {total > 0 && (
                <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mt: 3, flexWrap: 'wrap', gap: 2 }}>
                  <Typography variant="body2" color="textSecondary">
                    Page {page} of {totalPages}
                  </Typography>
                  <Pagination
                    count={totalPages}
                    page={page}
                    onChange={(_, newPage) => setPage(newPage)}
                    color="primary"
                    showFirstButton
                    showLastButton
                    size="large"
                  />
                </Box>
              )}
            </>
          )}
        </CardContent>
      </Card>

      {/* Update Package Dialog */}
      <Dialog 
        open={packageDialog.open} 
        onClose={handleClosePackageDialog}
        maxWidth="sm"
        fullWidth
      >
        <DialogTitle>Update Package for {packageDialog.userName}</DialogTitle>
        <DialogContent>
          <Box sx={{ pt: 2 }}>
            <FormControl fullWidth>
              <InputLabel>Select Package</InputLabel>
              <Select
                value={selectedPackageId}
                label="Select Package"
                onChange={(e) => setSelectedPackageId(e.target.value)}
                disabled={packagesLoading}
              >
                {packagesLoading ? (
                  <MenuItem disabled>
                    <CircularProgress size={20} sx={{ mr: 1 }} />
                    Loading packages...
                  </MenuItem>
                ) : packages.length === 0 ? (
                  <MenuItem disabled>No packages available</MenuItem>
                ) : (
                  packages.map((pkg) => (
                    <MenuItem key={pkg.id} value={String(pkg.id)}>
                      {pkg.name} {pkg.active ? '' : '(Inactive)'}
                    </MenuItem>
                  ))
                )}
              </Select>
            </FormControl>
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClosePackageDialog} disabled={updatingPackage}>
            Cancel
          </Button>
          <Button 
            onClick={handleUpdatePackage} 
            variant="contained"
            disabled={!selectedPackageId || updatingPackage || packagesLoading}
          >
            {updatingPackage ? <CircularProgress size={20} sx={{ mr: 1 }} /> : null}
            Update Package
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default UsersPage;

