import React, { useState, useEffect } from 'react';
import {
  Box,
  Card,
  CardContent,
  Typography,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Button,
  Chip,
  IconButton,
  MenuItem,
  Select,
  FormControl,
  InputLabel,
  Pagination,
  CircularProgress,
  Alert,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Avatar,
} from '@mui/material';
import { Add, Edit, Delete, Refresh } from '@mui/icons-material';
import { apiService } from '../../services/api';

interface AdContent {
  id: number;
  slot_code: string;
  image: string;
  image_url: string;
  url: string;
  date_from: string | null;
  date_to: string | null;
  title: string;
  active: boolean;
  created_at: string;
  updated_at: string;
}

const AdsContentsPage: React.FC = () => {
  const [contents, setContents] = useState<AdContent[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [success, setSuccess] = useState<string | null>(null);
  const [slotCodeFilter, setSlotCodeFilter] = useState('');
  const [activeFilter, setActiveFilter] = useState<boolean | null>(null);
  const [page, setPage] = useState(1);
  const [size, setSize] = useState(20);
  const [totalPages, setTotalPages] = useState(1);
  const [total, setTotal] = useState(0);
  const [baseUrl, setBaseUrl] = useState('');
  const [dialogOpen, setDialogOpen] = useState(false);
  const [editingContent, setEditingContent] = useState<AdContent | null>(null);
  const [deleteDialog, setDeleteDialog] = useState<{ open: boolean; contentId: number | null }>({
    open: false,
    contentId: null,
  });
  const [imagePreview, setImagePreview] = useState<string | null>(null);

  const [formData, setFormData] = useState({
    slot_code: '',
    title: '',
    url: '',
    active: true,
    date_from: '',
    date_to: '',
    image: null as File | null,
  });

  const fetchContents = async () => {
    setLoading(true);
    setError(null);
    try {
      const response = await apiService.getAdContents({
        slot_code: slotCodeFilter || undefined,
        active: activeFilter !== null ? activeFilter : undefined,
        page,
        size,
      });
      
      if (response.data) {
        const data = response.data as any;
        setContents(data.items || []);
        setTotalPages(data.pages || 1);
        setTotal(data.total || 0);
        setBaseUrl(data.base_url || '');
      }
    } catch (err: any) {
      console.error('Ad contents fetch error:', err);
      setError(err.message || 'Failed to fetch ad contents');
      setContents([]);
      setTotal(0);
      setTotalPages(1);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchContents();
  }, [page, size, slotCodeFilter, activeFilter]);

  // Get unique slot codes from contents for filter dropdown
  const uniqueSlotCodes = Array.from(new Set(contents.map(c => c.slot_code))).sort();

  const handleOpenDialog = (content?: AdContent) => {
    if (content) {
      setEditingContent(content);
      setFormData({
        slot_code: content.slot_code,
        title: content.title,
        url: content.url || '',
        active: content.active,
        date_from: content.date_from ? content.date_from.split('T')[0] : '',
        date_to: content.date_to ? content.date_to.split('T')[0] : '',
        image: null,
      });
      setImagePreview(content.image_url || null);
    } else {
      setEditingContent(null);
      setFormData({
        slot_code: '',
        title: '',
        url: '',
        active: true,
        date_from: '',
        date_to: '',
        image: null,
      });
      setImagePreview(null);
    }
    setDialogOpen(true);
  };

  const handleCloseDialog = () => {
    setDialogOpen(false);
    setEditingContent(null);
    setImagePreview(null);
  };

  const handleImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      setFormData({ ...formData, image: file });
      // Create preview URL
      const reader = new FileReader();
      reader.onloadend = () => {
        setImagePreview(reader.result as string);
      };
      reader.readAsDataURL(file);
    }
  };

  const handleSubmit = async () => {
    try {
      const formDataToSend = new FormData();
      if (editingContent) {
        if (formData.slot_code) formDataToSend.append('slot_code', formData.slot_code);
        if (formData.title) formDataToSend.append('title', formData.title);
        if (formData.url) formDataToSend.append('url', formData.url);
        formDataToSend.append('active', String(formData.active));
        if (formData.date_from) formDataToSend.append('date_from', formData.date_from);
        if (formData.date_to) formDataToSend.append('date_to', formData.date_to);
        if (formData.image) formDataToSend.append('image', formData.image);

        await apiService.updateAdContent(editingContent.id, formDataToSend);
        setSuccess('Ad content updated successfully');
      } else {
        formDataToSend.append('slot_code', formData.slot_code);
        formDataToSend.append('title', formData.title);
        if (formData.url) formDataToSend.append('url', formData.url);
        formDataToSend.append('active', String(formData.active));
        if (formData.date_from) formDataToSend.append('date_from', formData.date_from);
        if (formData.date_to) formDataToSend.append('date_to', formData.date_to);
        if (formData.image) formDataToSend.append('image', formData.image);

        await apiService.createAdContent(formDataToSend);
        setSuccess('Ad content created successfully');
      }
      
      handleCloseDialog();
      fetchContents();
    } catch (err: any) {
      setError(err.message || 'Failed to save ad content');
    }
  };

  const handleDelete = async () => {
    if (!deleteDialog.contentId) return;
    
    try {
      await apiService.deleteAdContent(deleteDialog.contentId);
      setSuccess('Ad content deleted successfully');
      setDeleteDialog({ open: false, contentId: null });
      fetchContents();
    } catch (err: any) {
      setError(err.message || 'Failed to delete ad content');
    }
  };

  return (
    <Box>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
        <Typography variant="h4">Ad Contents Management</Typography>
        <Button
          variant="contained"
          startIcon={<Add />}
          onClick={() => handleOpenDialog()}
        >
          Create Content
        </Button>
      </Box>

      <Card sx={{ mb: 3 }}>
        <CardContent>
          <Box sx={{ display: 'flex', gap: 2, alignItems: 'center' }}>
            <FormControl size="small" sx={{ minWidth: 200 }}>
              <InputLabel>Slot Code</InputLabel>
              <Select
                value={slotCodeFilter}
                label="Slot Code"
                onChange={(e) => {
                  setSlotCodeFilter(e.target.value);
                  setPage(1);
                }}
              >
                <MenuItem value="">All</MenuItem>
                {uniqueSlotCodes.map((slotCode) => (
                  <MenuItem key={slotCode} value={slotCode}>
                    {slotCode}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
            <FormControl size="small" sx={{ minWidth: 150 }}>
              <InputLabel>Status</InputLabel>
              <Select
                value={activeFilter === null ? '' : activeFilter}
                label="Status"
                onChange={(e) => {
                  setActiveFilter(e.target.value === '' ? null : e.target.value);
                  setPage(1);
                }}
              >
                <MenuItem value="">All</MenuItem>
                <MenuItem value={true}>Active</MenuItem>
                <MenuItem value={false}>Inactive</MenuItem>
              </Select>
            </FormControl>
            <IconButton onClick={fetchContents} color="primary">
              <Refresh />
            </IconButton>
          </Box>
        </CardContent>
      </Card>

      {error && (
        <Alert severity="error" sx={{ mb: 2 }} onClose={() => setError(null)}>
          {error}
        </Alert>
      )}

      {success && (
        <Alert severity="success" sx={{ mb: 2 }} onClose={() => setSuccess(null)}>
          {success}
        </Alert>
      )}

      <Card>
        <CardContent>
          <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2, flexWrap: 'wrap', gap: 2 }}>
            <Typography variant="h6">
              Ad Contents ({total})
            </Typography>
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
              <Typography variant="body2" color="textSecondary">
                {total > 0 ? (
                  `Showing ${(page - 1) * size + 1} - ${Math.min(page * size, total)} of ${total}`
                ) : (
                  'No contents'
                )}
              </Typography>
              <FormControl size="small" sx={{ minWidth: 100 }}>
                <InputLabel>Per Page</InputLabel>
                <Select
                  value={size}
                  label="Per Page"
                  onChange={(e) => {
                    setSize(Number(e.target.value));
                    setPage(1);
                  }}
                >
                  <MenuItem value={10}>10</MenuItem>
                  <MenuItem value={20}>20</MenuItem>
                  <MenuItem value={50}>50</MenuItem>
                  <MenuItem value={100}>100</MenuItem>
                </Select>
              </FormControl>
            </Box>
          </Box>

          {loading ? (
            <Box sx={{ display: 'flex', justifyContent: 'center', p: 4 }}>
              <CircularProgress />
            </Box>
          ) : (
            <>
              <TableContainer component={Paper} variant="outlined">
                <Table>
                  <TableHead>
                    <TableRow>
                      <TableCell>Image</TableCell>
                      <TableCell>Slot Code</TableCell>
                      <TableCell>Title</TableCell>
                      <TableCell>URL</TableCell>
                      <TableCell>Status</TableCell>
                      <TableCell>Date Range</TableCell>
                      <TableCell>Created At</TableCell>
                      <TableCell>Actions</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {contents.length === 0 ? (
                      <TableRow>
                        <TableCell colSpan={8} align="center">
                          <Typography color="textSecondary">No ad contents found</Typography>
                        </TableCell>
                      </TableRow>
                    ) : (
                      contents.map((content) => {
                        const imageUrl = content.image_url || (baseUrl && content.image ? `${baseUrl}${content.image}` : '');
                        return (
                          <TableRow key={content.id} hover>
                            <TableCell>
                              {imageUrl ? (
                                <Avatar 
                                  src={imageUrl} 
                                  variant="rounded" 
                                  sx={{ width: 60, height: 60, cursor: 'pointer' }}
                                  onClick={() => window.open(imageUrl, '_blank')}
                                />
                              ) : (
                                'N/A'
                              )}
                            </TableCell>
                            <TableCell>
                              <Chip label={content.slot_code} size="small" variant="outlined" />
                            </TableCell>
                            <TableCell>
                              <Typography variant="body2" fontWeight="medium">
                                {content.title}
                              </Typography>
                            </TableCell>
                            <TableCell>
                              {content.url ? (
                                <Typography 
                                  variant="body2" 
                                  sx={{ 
                                    maxWidth: 200, 
                                    overflow: 'hidden', 
                                    textOverflow: 'ellipsis',
                                    whiteSpace: 'nowrap',
                                    cursor: 'pointer',
                                    color: 'primary.main',
                                    textDecoration: 'underline'
                                  }}
                                  onClick={() => window.open(content.url, '_blank')}
                                >
                                  {content.url}
                                </Typography>
                              ) : (
                                'N/A'
                              )}
                            </TableCell>
                            <TableCell>
                              <Chip
                                label={content.active ? 'Active' : 'Inactive'}
                                color={content.active ? 'success' : 'default'}
                                size="small"
                              />
                            </TableCell>
                            <TableCell>
                              {content.date_from && content.date_to
                                ? `${new Date(content.date_from).toLocaleDateString()} - ${new Date(content.date_to).toLocaleDateString()}`
                                : 'No date range'}
                            </TableCell>
                            <TableCell>
                              <Typography variant="body2" fontSize="0.75rem">
                                {new Date(content.created_at).toLocaleString()}
                              </Typography>
                            </TableCell>
                            <TableCell>
                              <IconButton
                                size="small"
                                color="primary"
                                onClick={() => handleOpenDialog(content)}
                              >
                                <Edit />
                              </IconButton>
                              <IconButton
                                size="small"
                                color="error"
                                onClick={() => setDeleteDialog({ open: true, contentId: content.id })}
                              >
                                <Delete />
                              </IconButton>
                            </TableCell>
                          </TableRow>
                        );
                      })
                    )}
                  </TableBody>
                </Table>
              </TableContainer>

              {total > 0 && (
                <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mt: 3, flexWrap: 'wrap', gap: 2 }}>
                  <Typography variant="body2" color="textSecondary">
                    Page {page} of {totalPages}
                  </Typography>
                  <Pagination
                    count={totalPages}
                    page={page}
                    onChange={(_, newPage) => setPage(newPage)}
                    color="primary"
                    showFirstButton
                    showLastButton
                    size="large"
                  />
                </Box>
              )}
            </>
          )}
        </CardContent>
      </Card>

      <Dialog open={dialogOpen} onClose={handleCloseDialog} maxWidth="md" fullWidth>
        <DialogTitle>{editingContent ? 'Edit Ad Content' : 'Create Ad Content'}</DialogTitle>
        <DialogContent>
          <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2, mt: 1 }}>
            {editingContent ? (
              <TextField
                label="Slot Code"
                fullWidth
                value={formData.slot_code}
                disabled
              />
            ) : (
              <TextField
                label="Slot Code"
                fullWidth
                required
                value={formData.slot_code}
                onChange={(e) => setFormData({ ...formData, slot_code: e.target.value })}
                placeholder="e.g., home_ad_1"
                helperText={uniqueSlotCodes.length > 0 && `Existing: ${uniqueSlotCodes.join(', ')}`}
              />
            )}
            <TextField
              label="Title"
              fullWidth
              required={!editingContent}
              value={formData.title}
              onChange={(e) => setFormData({ ...formData, title: e.target.value })}
            />
            <TextField
              label="URL"
              fullWidth
              value={formData.url}
              onChange={(e) => setFormData({ ...formData, url: e.target.value })}
            />
            <TextField
              label="From Date"
              type="date"
              fullWidth
              InputLabelProps={{ shrink: true }}
              value={formData.date_from}
              onChange={(e) => setFormData({ ...formData, date_from: e.target.value })}
            />
            <TextField
              label="To Date"
              type="date"
              fullWidth
              InputLabelProps={{ shrink: true }}
              value={formData.date_to}
              onChange={(e) => setFormData({ ...formData, date_to: e.target.value })}
            />
            <FormControl fullWidth>
              <InputLabel>Status</InputLabel>
              <Select
                value={formData.active}
                label="Status"
                onChange={(e) => setFormData({ ...formData, active: e.target.value as boolean })}
              >
                <MenuItem value={true}>Active</MenuItem>
                <MenuItem value={false}>Inactive</MenuItem>
              </Select>
            </FormControl>
            <Button variant="outlined" component="label">
              {formData.image ? 'Change Image' : editingContent ? 'Update Image (Optional)' : 'Upload Image'}
              <input
                type="file"
                hidden
                accept="image/*"
                onChange={handleImageChange}
              />
            </Button>
            {imagePreview && (
              <Box sx={{ mt: 1 }}>
                <Typography variant="body2" color="textSecondary" sx={{ mb: 1 }}>
                  Image Preview:
                </Typography>
                <Box
                  component="img"
                  src={imagePreview}
                  alt="Preview"
                  sx={{
                    maxWidth: '100%',
                    maxHeight: 300,
                    objectFit: 'contain',
                    border: '1px solid',
                    borderColor: 'divider',
                    borderRadius: 1,
                  }}
                />
              </Box>
            )}
            {formData.image && !imagePreview && (
              <Typography variant="body2" color="textSecondary">
                Selected: {formData.image.name}
              </Typography>
            )}
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseDialog}>Cancel</Button>
          <Button onClick={handleSubmit} variant="contained">
            {editingContent ? 'Update' : 'Create'}
          </Button>
        </DialogActions>
      </Dialog>

      <Dialog open={deleteDialog.open} onClose={() => setDeleteDialog({ open: false, contentId: null })}>
        <DialogTitle>Delete Ad Content</DialogTitle>
        <DialogContent>
          <Typography>Are you sure you want to delete this ad content? This action cannot be undone.</Typography>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setDeleteDialog({ open: false, contentId: null })}>Cancel</Button>
          <Button onClick={handleDelete} variant="contained" color="error">
            Delete
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default AdsContentsPage;

