import React, { useState, useEffect } from 'react';
import {
  Box,
  Card,
  CardContent,
  Typography,
  TextField,
  Button,
  Switch,
  FormControlLabel,
  Alert,
  CircularProgress,
  Grid,
} from '@mui/material';
import { Save } from '@mui/icons-material';
import { apiService } from '../../services/api';

const ReferralConfigPage: React.FC = () => {
  const [loading, setLoading] = useState(false);
  const [saving, setSaving] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [success, setSuccess] = useState<string | null>(null);
  const [config, setConfig] = useState({
    first_referral_percentage: '',
    subsequent_referral_percentage: '',
    minimum_threshold: '',
    is_active: true,
  });

  const fetchConfig = async () => {
    setLoading(true);
    setError(null);
    try {
      const response = await apiService.getReferralConfig();
      
      if (response.data) {
        const data = response.data as any;
        setConfig({
          first_referral_percentage: data.first_referral_percentage || '',
          subsequent_referral_percentage: data.subsequent_referral_percentage || '',
          minimum_threshold: data.minimum_threshold || '',
          is_active: data.is_active !== undefined ? data.is_active : true,
        });
      }
    } catch (err: any) {
      setError(err.message || 'Failed to fetch referral config');
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchConfig();
  }, []);

  const handleSubmit = async () => {
    setSaving(true);
    setError(null);
    setSuccess(null);
    
    try {
      await apiService.updateReferralConfig({
        first_referral_percentage: config.first_referral_percentage || undefined,
        subsequent_referral_percentage: config.subsequent_referral_percentage || undefined,
        minimum_threshold: config.minimum_threshold || undefined,
        is_active: config.is_active,
      });
      
      setSuccess('Referral configuration updated successfully');
    } catch (err: any) {
      setError(err.message || 'Failed to update referral config');
    } finally {
      setSaving(false);
    }
  };

  if (loading) {
    return (
      <Box sx={{ display: 'flex', justifyContent: 'center', p: 4 }}>
        <CircularProgress />
      </Box>
    );
  }

  return (
    <Box>
      <Typography variant="h4" gutterBottom>
        Referral Configuration
      </Typography>

      {error && (
        <Alert severity="error" sx={{ mb: 2 }} onClose={() => setError(null)}>
          {error}
        </Alert>
      )}

      {success && (
        <Alert severity="success" sx={{ mb: 2 }} onClose={() => setSuccess(null)}>
          {success}
        </Alert>
      )}

      <Card>
        <CardContent>
          <Grid container spacing={3}>
            <Grid item xs={12} md={6}>
              <TextField
                label="First Referral Percentage"
                fullWidth
                type="number"
                value={config.first_referral_percentage}
                onChange={(e) => setConfig({ ...config, first_referral_percentage: e.target.value })}
                helperText="Percentage for first referral (e.g., 10.00 for 10%)"
                InputProps={{
                  endAdornment: '%',
                }}
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <TextField
                label="Subsequent Referral Percentage"
                fullWidth
                type="number"
                value={config.subsequent_referral_percentage}
                onChange={(e) => setConfig({ ...config, subsequent_referral_percentage: e.target.value })}
                helperText="Percentage for subsequent referrals (e.g., 5.00 for 5%)"
                InputProps={{
                  endAdornment: '%',
                }}
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <TextField
                label="Minimum Threshold"
                fullWidth
                type="number"
                value={config.minimum_threshold}
                onChange={(e) => setConfig({ ...config, minimum_threshold: e.target.value })}
                helperText="Minimum amount referee must add (e.g., 50.00)"
                InputProps={{
                  startAdornment: '',
                }}
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <FormControlLabel
                control={
                  <Switch
                    checked={config.is_active}
                    onChange={(e) => setConfig({ ...config, is_active: e.target.checked })}
                  />
                }
                label="Enable Referral System"
              />
            </Grid>
            <Grid item xs={12}>
              <Button
                variant="contained"
                startIcon={<Save />}
                onClick={handleSubmit}
                disabled={saving}
                size="large"
              >
                {saving ? 'Saving...' : 'Save Configuration'}
              </Button>
            </Grid>
          </Grid>
        </CardContent>
      </Card>
    </Box>
  );
};

export default ReferralConfigPage;

