import {
  AppBar,
  Box,
  Drawer,
  IconButton,
  List,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  Toolbar,
  Typography,
} from "@mui/material";
import {
  Menu as MenuIcon,
  Logout,
  Dashboard,
  People,
  AccountBalanceWallet,
  ReceiptLong,
  LocalOffer,
  ViewCarousel,
  Hub,
  NotificationsActive,
  Send,
  ChatBubble,
  LocationOn,
  Inventory,
  SupportAgent,
  Settings,
} from "@mui/icons-material";
import { useState } from "react";
import { Outlet, useLocation, useNavigate } from "react-router-dom";
import { useAuth } from "../auth/AuthContext";

const drawerWidth = 260;

const navItems = [
  { label: "Overview", icon: <Dashboard />, path: "/overview" },
  { label: "Users", icon: <People />, path: "/users" },
  { label: "Wallets", icon: <AccountBalanceWallet />, path: "/wallets" },
  { label: "Wallet Txns", icon: <ReceiptLong />, path: "/wallet-transactions" },
  { label: "Recharge Txns", icon: <ReceiptLong />, path: "/recharge-transactions" },
  { label: "Fund Requests", icon: <ReceiptLong />, path: "/fund-requests" },
  { label: "Offers", icon: <LocalOffer />, path: "/offers" },
  { label: "Ad Slots", icon: <ViewCarousel />, path: "/ads/slots" },
  { label: "Ad Contents", icon: <ViewCarousel />, path: "/ads/contents" },
  { label: "Referral Config", icon: <Hub />, path: "/referral-config" },
  { label: "Packages", icon: <Inventory />, path: "/packages" },
  { label: "FCM", icon: <NotificationsActive />, path: "/fcm" },
  { label: "Push Notifications", icon: <Send />, path: "/notifications" },
  { label: "WhatsApp Messages", icon: <ChatBubble />, path: "/whatsapp" },
  { label: "User Locations", icon: <LocationOn />, path: "/location/users" },
  { label: "Support Chats", icon: <SupportAgent />, path: "/support/chats" },
  { label: "Settings", icon: <Settings />, path: "/settings" },
];

export const DashboardLayout = () => {
  const [mobileOpen, setMobileOpen] = useState(false);
  const navigate = useNavigate();
  const location = useLocation();
  const { admin, logout } = useAuth();

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const drawer = (
    <Box sx={{ height: "100%", display: "flex", flexDirection: "column" }}>
      <Box sx={{ p: 3, borderBottom: "1px solid rgba(148, 163, 184, 0.2)" }}>
        <Typography variant="h6" fontWeight={700} color="primary.main">
          Laddoo Admin
        </Typography>
        <Typography variant="body2" color="text.secondary">
          Recharge Dashboard
        </Typography>
      </Box>
      <Box sx={{ flexGrow: 1, overflowY: "auto" }}>
        <List>
          {navItems.map((item) => (
            <ListItemButton
              key={item.path}
              onClick={() => navigate(item.path)}
              selected={location.pathname === item.path}
              sx={{
                borderRadius: 2,
                mx: 1.5,
                mb: 0.5,
                "&.Mui-selected": {
                  bgcolor: "primary.main",
                  color: "#F9FAFB",
                  "& .MuiListItemIcon-root": { color: "#F9FAFB" },
                },
              }}
            >
              <ListItemIcon sx={{ color: "text.secondary", minWidth: 40 }}>
                {item.icon}
              </ListItemIcon>
              <ListItemText primary={item.label} />
            </ListItemButton>
          ))}
        </List>
      </Box>
      <Box sx={{ borderTop: "1px solid rgba(148, 163, 184, 0.2)", p: 2 }}>
        <Box display="flex" alignItems="center" justifyContent="space-between" mb={1}>
          <Box>
            <Typography variant="body2" fontWeight={600}>
              {admin?.name ?? admin?.email ?? "Admin"}
            </Typography>
            <Typography variant="caption" color="text.secondary">
              Super Admin
            </Typography>
          </Box>
          <IconButton color="inherit" size="small" onClick={logout}>
            <Logout fontSize="small" />
          </IconButton>
        </Box>
      </Box>
    </Box>
  );

  return (
    <Box sx={{ display: "flex", minHeight: "100vh", bgcolor: "background.default" }}>
      <AppBar
        position="fixed"
        sx={{
          zIndex: (theme) => theme.zIndex.drawer + 1,
          bgcolor: "rgba(15,23,42,0.8)",
          backdropFilter: "blur(16px)",
          borderBottom: "1px solid rgba(148, 163, 184, 0.3)",
        }}
      >
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={handleDrawerToggle}
            sx={{ mr: 2, display: { sm: "none" } }}
          >
            <MenuIcon />
          </IconButton>
          <Typography variant="h6" noWrap component="div">
            Laddoo Admin Dashboard
          </Typography>
        </Toolbar>
      </AppBar>
      <Box
        component="nav"
        sx={{ width: { sm: drawerWidth }, flexShrink: { sm: 0 } }}
        aria-label="navigation"
      >
        <Drawer
          variant="temporary"
          open={mobileOpen}
          onClose={handleDrawerToggle}
          ModalProps={{
            keepMounted: true,
          }}
          sx={{
            display: { xs: "block", sm: "none" },
            "& .MuiDrawer-paper": {
              boxSizing: "border-box",
              width: drawerWidth,
              bgcolor: "background.paper",
            },
          }}
        >
          {drawer}
        </Drawer>
        <Drawer
          variant="permanent"
          sx={{
            display: { xs: "none", sm: "block" },
            "& .MuiDrawer-paper": {
              boxSizing: "border-box",
              width: drawerWidth,
              bgcolor: "background.paper",
            },
          }}
          open
        >
          {drawer}
        </Drawer>
      </Box>
      <Box
        component="main"
        sx={{
          flexGrow: 1,
          p: 3,
          mt: 8,
          width: { sm: `calc(100% - ${drawerWidth}px)` },
        }}
      >
        <Outlet />
      </Box>
    </Box>
  );
};


