import React, { useState, useEffect } from 'react';
import {
  Box,
  Card,
  CardContent,
  Typography,
  TextField,
  Button,
  Alert,
  CircularProgress,
  Autocomplete,
  Chip,
  Divider,
  Paper,
  Grid,
  IconButton,
  Switch,
  FormControlLabel,
  Accordion,
  AccordionSummary,
  AccordionDetails,
} from '@mui/material';
import { Send, Person, Message, ContentCopy, CheckCircle, ExpandMore, AttachFile } from '@mui/icons-material';
import { apiService } from '../../services/api';

interface User {
  id: string;
  username: string;
  email?: string;
  phone: string;
}

interface MessagePreset {
  category: string;
  icon: string;
  messages: string[];
}

const MESSAGE_PRESETS: MessagePreset[] = [
  {
    category: ' Promotional Messages',
    icon: '',
    messages: [
      `Recharge your mobile or DTH and earn cashback.
Recharge 149 and get up to 25 cashback.
Recharge now with Laddoo App.
Download: https://play.google.com/store/apps/details?id=com.mkfintech.laddooapp`,

      `Save on your next recharge with Laddoo App.
Recharge 199 and get up to 30 cashback.
Recharge now with Laddoo App.
Download: https://play.google.com/store/apps/details?id=com.mkfintech.laddooapp`,

      `Make your recharges more rewarding.
Recharge 299 and get up to 45 cashback.
Recharge now with Laddoo App.
Download: https://play.google.com/store/apps/details?id=com.mkfintech.laddooapp`,

      `Exclusive recharge benefit for Laddoo App users.
Recharge 399 and get up to 60 cashback.
Recharge now with Laddoo App.
Download: https://play.google.com/store/apps/details?id=com.mkfintech.laddooapp`,

      `Earn cashback on every recharge.
Recharge 499 and get up to 75 cashback.
Recharge now with Laddoo App.
Download: https://play.google.com/store/apps/details?id=com.mkfintech.laddooapp`,

      `Recharge and receive cashback instantly.
Available cashback offers:
149  up to 25
199  up to 30
299  up to 45
Recharge now with Laddoo App.
Download: https://play.google.com/store/apps/details?id=com.mkfintech.laddooapp`,

      `Your everyday recharge app just got better.
Get cashback on mobile and DTH recharges.
Recharge now with Laddoo App.
Download: https://play.google.com/store/apps/details?id=com.mkfintech.laddooapp`,

      `Do your next recharge with Laddoo App and earn cashback.
Fast, secure, and convenient.
Recharge now with Laddoo App.
Download: https://play.google.com/store/apps/details?id=com.mkfintech.laddooapp`,

      `Don't miss cashback on your next recharge.
Recharge today and earn rewards.
Recharge now with Laddoo App.
Download: https://play.google.com/store/apps/details?id=com.mkfintech.laddooapp`,

      `Choose a smarter way to recharge.
Enjoy cashback on eligible recharges.
Recharge now with Laddoo App.
Download: https://play.google.com/store/apps/details?id=com.mkfintech.laddooapp`,
    ],
  },
];

const WhatsAppMessagesPage: React.FC = () => {
  const [selectedUser, setSelectedUser] = useState<User | null>(null);
  const [userId, setUserId] = useState('');
  const [message, setMessage] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [success, setSuccess] = useState<string | null>(null);
  const [users, setUsers] = useState<User[]>([]);
  const [usersLoading, setUsersLoading] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');
  const [selectedPreset, setSelectedPreset] = useState<string | null>(null);
  
  // WhatsApp options
  const [enableLinkPreview, setEnableLinkPreview] = useState(false);
  const [delay, setDelay] = useState('');
  const [mentions, setMentions] = useState('');
  
  // Offer image selection
  const [selectedImage, setSelectedImage] = useState<File | null>(null);
  const [imagePreview, setImagePreview] = useState<string | null>(null);
  const [selectedImageName, setSelectedImageName] = useState<string | null>(null);
  
  // Available offer images - images should be in public/Offers/ directory
  // Update this list when you add more images
  const offerImages = [
    { name: 'Offer 1', path: '/Offers/offer1.png', filename: 'offer1.png' },
    { name: 'Offer 2', path: '/Offers/offer2.png', filename: 'offer2.png' },
  ];

  // Fetch users for autocomplete
  const fetchUsers = async (search: string = '') => {
    if (search.length < 2) {
      setUsers([]);
      return;
    }

    setUsersLoading(true);
    try {
      const response = await apiService.getUsers({
        search: search,
        page: 1,
        size: 20,
      });
      
      if (response.data) {
        const data = response.data as any;
        setUsers(data.items || []);
      }
    } catch (err) {
      console.error('Failed to fetch users:', err);
      setUsers([]);
    } finally {
      setUsersLoading(false);
    }
  };

  useEffect(() => {
    const debounceTimer = setTimeout(() => {
      if (searchQuery) {
        fetchUsers(searchQuery);
      } else {
        setUsers([]);
      }
    }, 300);

    return () => clearTimeout(debounceTimer);
  }, [searchQuery]);

  const handleImageSelect = async (imagePath: string, filename: string) => {
    try {
      // Fetch the image from the public folder
      const response = await fetch(imagePath);
      if (!response.ok) {
        throw new Error('Image not found');
      }
      const blob = await response.blob();
      const file = new File([blob], filename, { type: blob.type || 'image/png' });
      
      setSelectedImage(file);
      setSelectedImageName(filename);
      const previewUrl = URL.createObjectURL(file);
      setImagePreview(previewUrl);
      setError(null);
    } catch (err) {
      console.error('Failed to load image:', err);
      setError(`Failed to load image: ${filename}. Make sure the image is in public/Offers/ directory.`);
    }
  };

  const handleSend = async () => {
    if (!userId.trim()) {
      setError('Please enter or select a user ID');
      return;
    }

    // If image is selected, use media API (message is optional caption)
    if (selectedImage) {
      if (!message.trim()) {
        setError('Please enter a caption for the image');
        return;
      }

      setLoading(true);
      setError(null);
      setSuccess(null);

      try {
        // Determine media type from file
        const mediaType = selectedImage.type.startsWith('image/') ? 'image' :
                         selectedImage.type.startsWith('video/') ? 'video' :
                         selectedImage.type.startsWith('audio/') ? 'audio' : 'document';

        await apiService.sendWhatsAppMedia(
          userId.trim(),
          mediaType as 'image' | 'video' | 'audio' | 'document',
          selectedImage,
          message.trim()
        );
        setSuccess('WhatsApp media message sent successfully!');
        setMessage('');
        setSelectedUser(null);
        setUserId('');
        setSearchQuery('');
        setSelectedPreset(null);
        setSelectedImage(null);
        setSelectedImageName(null);
        if (imagePreview) {
          URL.revokeObjectURL(imagePreview);
        }
        setImagePreview(null);
      } catch (err: any) {
        setError(err.message || 'Failed to send WhatsApp media message');
      } finally {
        setLoading(false);
      }
      return;
    }

    // Otherwise, send text message
    if (!message.trim()) {
      setError('Please enter a message');
      return;
    }

    setLoading(true);
    setError(null);
    setSuccess(null);

    try {
      const options: any = {};
      
      if (enableLinkPreview) {
        options.linkPreview = true;
      }
      
      if (delay && !isNaN(Number(delay)) && Number(delay) > 0) {
        options.delay = Number(delay);
      }
      
      if (mentions.trim()) {
        // Split by comma and clean up phone numbers
        const phoneNumbers = mentions
          .split(',')
          .map(p => p.trim())
          .filter(p => p.length > 0);
        if (phoneNumbers.length > 0) {
          options.mentions = phoneNumbers;
        }
      }

      await apiService.sendWhatsAppMessage(userId.trim(), message.trim(), options);
      setSuccess('WhatsApp message sent successfully!');
      setMessage('');
      setSelectedUser(null);
      setUserId('');
      setSearchQuery('');
      setSelectedPreset(null);
      setEnableLinkPreview(false);
      setDelay('');
      setMentions('');
    } catch (err: any) {
      setError(err.message || 'Failed to send WhatsApp message');
    } finally {
      setLoading(false);
    }
  };

  const handleUserSelect = (user: User | null) => {
    setSelectedUser(user);
    if (user) {
      setUserId(user.id);
      setSearchQuery(user.username || user.email || user.phone);
    } else {
      setUserId('');
      setSearchQuery('');
    }
  };

  return (
    <Box>
      <Typography variant="h4" gutterBottom>
        WhatsApp Messages
      </Typography>
      <Typography variant="body2" color="textSecondary" sx={{ mb: 3 }}>
        Send WhatsApp messages to users directly from the admin panel
      </Typography>

      {error && (
        <Alert severity="error" sx={{ mb: 2 }} onClose={() => setError(null)}>
          {error}
        </Alert>
      )}

      {success && (
        <Alert severity="success" sx={{ mb: 2 }} onClose={() => setSuccess(null)}>
          {success}
        </Alert>
      )}

      <Card sx={{ mb: 3 }}>
        <CardContent>
          <Box sx={{ display: 'flex', flexDirection: 'column', gap: 4 }}>
            {/* User Selection */}
            <Box>
              <Typography variant="h6" gutterBottom sx={{ display: 'flex', alignItems: 'center', gap: 1, mb: 2 }}>
                <Person color="primary" />
                Select User
              </Typography>
              <Typography variant="body2" color="textSecondary" sx={{ mb: 3 }}>
                Search for a user by username, email, or phone number, or enter a user ID directly
              </Typography>
              
              <Box sx={{ display: 'flex', gap: 2, flexWrap: 'wrap' }}>
                <Autocomplete
                  sx={{ flexGrow: 1, minWidth: 300 }}
                  options={users}
                  getOptionLabel={(option) => 
                    `${option.username}${option.email ? ` (${option.email})` : ''} - ${option.phone}`
                  }
                  loading={usersLoading}
                  value={selectedUser}
                  onChange={(_, newValue) => handleUserSelect(newValue)}
                  inputValue={searchQuery}
                  onInputChange={(_, newInputValue) => {
                    setSearchQuery(newInputValue);
                    if (!newInputValue) {
                      setSelectedUser(null);
                      setUserId('');
                    }
                  }}
                  renderInput={(params) => (
                    <TextField
                      {...params}
                      label="Search User"
                      placeholder="Type username, email, or phone..."
                      variant="outlined"
                    />
                  )}
                  renderOption={(props, option) => (
                    <Box component="li" {...props} key={option.id}>
                      <Box sx={{ display: 'flex', flexDirection: 'column', width: '100%' }}>
                        <Typography variant="body1" fontWeight="medium">
                          {option.username}
                        </Typography>
                        <Box sx={{ display: 'flex', gap: 1, mt: 0.5 }}>
                          {option.email && (
                            <Chip label={option.email} size="small" variant="outlined" />
                          )}
                          <Chip label={option.phone} size="small" variant="outlined" />
                          <Chip 
                            label={`ID: ${option.id.substring(0, 8)}...`} 
                            size="small" 
                            variant="outlined"
                            sx={{ fontFamily: 'monospace', fontSize: '0.7rem' }}
                          />
                        </Box>
                      </Box>
                    </Box>
                  )}
                  noOptionsText={searchQuery.length < 2 ? 'Type at least 2 characters to search' : 'No users found'}
                />

                <TextField
                  label="Or Enter User ID"
                  variant="outlined"
                  value={userId}
                  onChange={(e) => {
                    setUserId(e.target.value);
                    if (e.target.value) {
                      setSelectedUser(null);
                      setSearchQuery('');
                    }
                  }}
                  placeholder="User ID"
                  sx={{ minWidth: 250 }}
                  helperText="Enter user ID directly if you know it"
                />
              </Box>

              {selectedUser && (
                <Box sx={{ mt: 3, p: 2.5, bgcolor: 'primary.light', borderRadius: 2, border: '1px solid', borderColor: 'primary.main' }}>
                  <Typography variant="body2" fontWeight="medium" color="primary.dark" gutterBottom>
                     Selected User:
                  </Typography>
                  <Box sx={{ display: 'flex', gap: 1, flexWrap: 'wrap', mt: 1.5 }}>
                    <Chip label={`Username: ${selectedUser.username}`} size="small" color="primary" />
                    {selectedUser.email && (
                      <Chip label={`Email: ${selectedUser.email}`} size="small" color="primary" />
                    )}
                    <Chip label={`Phone: ${selectedUser.phone}`} size="small" color="primary" />
                    <Chip 
                      label={`ID: ${selectedUser.id}`} 
                      size="small" 
                      color="primary"
                      sx={{ fontFamily: 'monospace' }}
                    />
                  </Box>
                </Box>
              )}
            </Box>

            <Divider />

            {/* Offer Image Selection */}
            <Box>
              <Typography variant="h6" gutterBottom sx={{ display: 'flex', alignItems: 'center', gap: 1, mb: 2 }}>
                <AttachFile color="primary" />
                Select Offer Image (Optional)
              </Typography>
              <Typography variant="body2" color="textSecondary" sx={{ mb: 2 }}>
                Choose an offer image to send as media. If selected, the message will be sent as image with caption.
              </Typography>
              
              <Grid container spacing={2}>
                {offerImages.map((offer, index) => (
                  <Grid item xs={12} sm={6} md={4} key={index}>
                    <Card
                      variant="outlined"
                      sx={{
                        cursor: 'pointer',
                        border: selectedImageName === offer.filename ? '2px solid' : '1px solid',
                        borderColor: selectedImageName === offer.filename ? 'primary.main' : 'divider',
                        bgcolor: selectedImageName === offer.filename ? 'primary.light' : 'background.paper',
                        transition: 'all 0.2s',
                        '&:hover': {
                          borderColor: 'primary.main',
                          transform: 'translateY(-2px)',
                          boxShadow: 2,
                        },
                      }}
                      onClick={() => handleImageSelect(offer.path, offer.filename)}
                    >
                      <CardContent>
                        <Box
                          component="img"
                          src={offer.path}
                          alt={offer.name}
                          sx={{
                            width: '100%',
                            height: 150,
                            objectFit: 'contain',
                            borderRadius: 1,
                            mb: 1,
                          }}
                          onError={(e) => {
                            (e.target as HTMLImageElement).style.display = 'none';
                          }}
                        />
                        <Typography variant="body2" align="center" fontWeight="medium">
                          {offer.name}
                        </Typography>
                        {selectedImageName === offer.filename && (
                          <Box sx={{ display: 'flex', justifyContent: 'center', mt: 1 }}>
                            <CheckCircle color="primary" fontSize="small" />
                          </Box>
                        )}
                      </CardContent>
                    </Card>
                  </Grid>
                ))}
              </Grid>

              {imagePreview && (
                <Box sx={{ mt: 2, p: 2, bgcolor: 'action.hover', borderRadius: 2 }}>
                  <Typography variant="body2" fontWeight="medium" gutterBottom>
                    Selected Image Preview:
                  </Typography>
                  <Box
                    component="img"
                    src={imagePreview}
                    alt="Selected offer"
                    sx={{
                      maxWidth: '100%',
                      maxHeight: 200,
                      objectFit: 'contain',
                      borderRadius: 1,
                      mt: 1,
                    }}
                  />
                  <Button
                    size="small"
                    color="error"
                    onClick={() => {
                      setSelectedImage(null);
                      if (imagePreview) {
                        URL.revokeObjectURL(imagePreview);
                      }
                      setImagePreview(null);
                    }}
                    sx={{ mt: 1 }}
                  >
                    Remove Image
                  </Button>
                </Box>
              )}
            </Box>

            <Divider />

            {/* Message Section with Presets */}
            <Box>
              <Typography variant="h6" gutterBottom sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                <Message color="primary" />
                WhatsApp Message
              </Typography>
              <Typography variant="body2" color="textSecondary" sx={{ mb: 3 }}>
                Select a promotional message from the list below or type your own custom message
              </Typography>
              
              <Grid container spacing={3}>
                {/* Preset Messages - Left Side */}
                <Grid item xs={12} md={5}>
                  <Card variant="outlined" sx={{ height: '100%', bgcolor: 'background.paper' }}>
                    <CardContent>
                      <Typography variant="h6" fontWeight="bold" gutterBottom sx={{ mb: 1 }}>
                         Promotional Messages
                      </Typography>
                      <Typography variant="body2" color="textSecondary" sx={{ mb: 2 }}>
                        Click on any message to select it
                      </Typography>
                      
                      <Box sx={{ maxHeight: 500, overflowY: 'auto', pr: 1 }}>
                        {MESSAGE_PRESETS[0] && (
                          <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
                            {MESSAGE_PRESETS[0].messages.map((msg, msgIndex) => {
                              const isSelected = selectedPreset === msg;
                              return (
                                <Paper
                                  key={msgIndex}
                                  elevation={isSelected ? 4 : 1}
                                  sx={{
                                    p: 2.5,
                                    cursor: 'pointer',
                                    border: '2px solid',
                                    borderColor: isSelected ? 'primary.main' : 'divider',
                                    borderRadius: 2,
                                    bgcolor: isSelected ? 'primary.light' : 'background.paper',
                                    transition: 'all 0.2s',
                                    '&:hover': {
                                      borderColor: 'primary.main',
                                      bgcolor: isSelected ? 'primary.light' : 'action.hover',
                                      transform: 'translateY(-2px)',
                                      boxShadow: 4,
                                    },
                                  }}
                                  onClick={() => {
                                    setMessage(msg);
                                    setSelectedPreset(msg);
                                  }}
                                >
                                  <Box sx={{ display: 'flex', alignItems: 'flex-start', gap: 1.5 }}>
                                    <Box sx={{ 
                                      minWidth: 32, 
                                      height: 32, 
                                      borderRadius: '50%', 
                                      bgcolor: isSelected ? 'primary.main' : 'action.selected',
                                      display: 'flex',
                                      alignItems: 'center',
                                      justifyContent: 'center',
                                      color: isSelected ? 'white' : 'text.secondary',
                                      fontWeight: 'bold',
                                      fontSize: '0.875rem'
                                    }}>
                                      {msgIndex + 1}
                                    </Box>
                                    <Box sx={{ flexGrow: 1, minWidth: 0 }}>
                                      <Typography
                                        variant="body2"
                                        sx={{
                                          wordBreak: 'break-word',
                                          fontWeight: isSelected ? 600 : 400,
                                          whiteSpace: 'pre-line',
                                          lineHeight: 1.6,
                                          color: isSelected ? 'primary.dark' : 'text.primary',
                                        }}
                                      >
                                        {msg}
                                      </Typography>
                                    </Box>
                                    <Box sx={{ display: 'flex', flexDirection: 'column', gap: 0.5 }}>
                                      {isSelected && (
                                        <CheckCircle color="primary" sx={{ fontSize: 20 }} />
                                      )}
                                      <IconButton
                                        size="small"
                                        onClick={(e) => {
                                          e.stopPropagation();
                                          navigator.clipboard.writeText(msg);
                                        }}
                                        sx={{ 
                                          opacity: 0.6, 
                                          '&:hover': { opacity: 1 },
                                          p: 0.5
                                        }}
                                      >
                                        <ContentCopy fontSize="small" />
                                      </IconButton>
                                    </Box>
                                  </Box>
                                </Paper>
                              );
                            })}
                          </Box>
                        )}
                      </Box>
                    </CardContent>
                  </Card>
                </Grid>

                {/* Message Input - Right Side */}
                <Grid item xs={12} md={7}>
                  <Card variant="outlined" sx={{ height: '100%', bgcolor: 'background.paper' }}>
                    <CardContent>
                      <Typography variant="h6" fontWeight="bold" gutterBottom sx={{ mb: 1 }}>
                         Edit Your Message
                      </Typography>
                      <Typography variant="body2" color="textSecondary" sx={{ mb: 2 }}>
                        Edit the selected message or type your own custom message
                      </Typography>
                      
                      <TextField
                        label={selectedImage ? "Image Caption" : "WhatsApp Message"}
                        multiline
                        rows={12}
                        fullWidth
                        value={message}
                        onChange={(e) => {
                          setMessage(e.target.value);
                          if (e.target.value !== selectedPreset) {
                            setSelectedPreset(null);
                          }
                        }}
                        placeholder={selectedImage 
                          ? "Enter caption for the image (required when sending image)..." 
                          : "Select a promotional message from the left or type your own custom message..."}
                        variant="outlined"
                        helperText={selectedImage 
                          ? `${message.length} characters - This will be sent as image caption` 
                          : `${message.length} characters`}
                        sx={{
                          '& .MuiOutlinedInput-root': {
                            fontSize: '0.95rem',
                            lineHeight: 1.8,
                            fontFamily: 'monospace',
                          },
                          '& .MuiInputBase-input': {
                            whiteSpace: 'pre-wrap',
                          },
                        }}
                      />
                    </CardContent>
                  </Card>
                </Grid>
              </Grid>
            </Box>

            <Divider />

            {/* WhatsApp Options */}
            <Box>
              <Accordion>
                <AccordionSummary expandIcon={<ExpandMore />}>
                  <Typography variant="h6" sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                    <AttachFile color="primary" />
                    Advanced Options (Optional)
                  </Typography>
                </AccordionSummary>
                <AccordionDetails>
                  <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
                    <FormControlLabel
                      control={
                        <Switch
                          checked={enableLinkPreview}
                          onChange={(e) => setEnableLinkPreview(e.target.checked)}
                        />
                      }
                      label="Enable Link Preview"
                    />
                    <Typography variant="body2" color="textSecondary" sx={{ ml: 4, mt: -1 }}>
                      Generate link previews for URLs in the message
                    </Typography>

                    <TextField
                      label="Delay (milliseconds)"
                      type="number"
                      value={delay}
                      onChange={(e) => setDelay(e.target.value)}
                      helperText="Optional delay before sending the message (e.g., 2000 for 2 seconds)"
                      sx={{ maxWidth: 300 }}
                    />

                    <TextField
                      label="Mentions (comma-separated phone numbers)"
                      value={mentions}
                      onChange={(e) => setMentions(e.target.value)}
                      placeholder="e.g., 917726828026, 919876543210"
                      helperText="Phone numbers to mention in the message (without + or spaces)"
                      fullWidth
                    />
                  </Box>
                </AccordionDetails>
              </Accordion>
            </Box>

            <Divider />

            {/* Send Button */}
            <Box sx={{ display: 'flex', justifyContent: 'flex-end', gap: 2 }}>
              <Button
                variant="outlined"
                onClick={() => {
                  setMessage('');
                  setSelectedUser(null);
                  setUserId('');
                  setSearchQuery('');
                  setSelectedPreset(null);
                  setError(null);
                  setSuccess(null);
                  setEnableLinkPreview(false);
                  setDelay('');
                  setMentions('');
                  setSelectedImage(null);
                  setSelectedImageName(null);
                  if (imagePreview) {
                    URL.revokeObjectURL(imagePreview);
                  }
                  setImagePreview(null);
                }}
                disabled={loading}
              >
                Clear All
              </Button>
              <Button
                variant="contained"
                size="large"
                startIcon={loading ? <CircularProgress size={20} color="inherit" /> : <Send />}
                onClick={handleSend}
                disabled={loading || !userId.trim() || !message.trim()}
                sx={{ minWidth: 150 }}
              >
                {loading ? 'Sending...' : selectedImage ? 'Send WhatsApp Image' : 'Send WhatsApp Message'}
              </Button>
            </Box>
          </Box>
        </CardContent>
      </Card>

      {/* Help Card */}
      <Card sx={{ mt: 3 }}>
        <CardContent>
          <Typography variant="h6" gutterBottom>
            How to Send WhatsApp Messages
          </Typography>
          <Box component="ul" sx={{ pl: 2, m: 0 }}>
            <Typography component="li" variant="body2" color="textSecondary" sx={{ mb: 1 }}>
              Search for a user by typing their username, email, or phone number in the search box
            </Typography>
            <Typography component="li" variant="body2" color="textSecondary" sx={{ mb: 1 }}>
              Or enter the user ID directly in the "User ID" field if you know it
            </Typography>
            <Typography component="li" variant="body2" color="textSecondary" sx={{ mb: 1 }}>
              <strong>Select a preset message</strong> from the categories below, or type your own custom message
            </Typography>
            <Typography component="li" variant="body2" color="textSecondary" sx={{ mb: 1 }}>
              You can edit preset messages after selecting them
            </Typography>
            <Typography component="li" variant="body2" color="textSecondary" sx={{ mb: 1 }}>
              Use advanced options to enable link previews, add delays, or mention users
            </Typography>
            <Typography component="li" variant="body2" color="textSecondary">
              Click "Send WhatsApp Message" to deliver the message to the user's WhatsApp
            </Typography>
          </Box>
        </CardContent>
      </Card>
    </Box>
  );
};

export default WhatsAppMessagesPage;
