import React from 'react';
import {
  Drawer,
  Box,
  Typography,
  IconButton,
  Divider,
  Chip,
  Paper,
  Modal,
  Backdrop,
  Fade,
  Button,
  CircularProgress,
} from '@mui/material';
import { Close, ZoomIn, ContentCopy, TextFields } from '@mui/icons-material';
import { createWorker } from 'tesseract.js';

interface SlideInPanelProps {
  open: boolean;
  onClose: () => void;
  title: string;
  children: React.ReactNode;
  width?: number | string;
}

export const SlideInPanel: React.FC<SlideInPanelProps> = ({
  open,
  onClose,
  title,
  children,
  width = 500,
}) => {
  return (
    <Drawer
      anchor="right"
      open={open}
      onClose={onClose}
      ModalProps={{
        keepMounted: false,
      }}
      sx={{
        zIndex: 1300,
        '& .MuiDrawer-paper': {
          width: width,
          maxWidth: '90vw',
          boxSizing: 'border-box',
          bgcolor: 'background.paper',
        },
      }}
    >
      <Box sx={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
        {/* Header */}
        <Box
          sx={{
            p: 2,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'space-between',
            borderBottom: '1px solid',
            borderColor: 'divider',
          }}
        >
          <Typography variant="h6" fontWeight="bold">
            {title}
          </Typography>
          <IconButton onClick={onClose} size="small">
            <Close />
          </IconButton>
        </Box>

        {/* Content */}
        <Box
          sx={{
            flex: 1,
            overflowY: 'auto',
            p: 3,
          }}
        >
          {children}
        </Box>
      </Box>
    </Drawer>
  );
};

interface DetailRowProps {
  label: string;
  value: React.ReactNode;
  copyable?: boolean;
}

export const DetailRow: React.FC<DetailRowProps> = ({ label, value, copyable = false }) => {
  const handleCopy = () => {
    if (typeof value === 'string') {
      navigator.clipboard.writeText(value);
    }
  };

  return (
    <Box sx={{ mb: 2 }}>
      <Typography variant="caption" color="textSecondary" sx={{ mb: 0.5, display: 'block' }}>
        {label}
      </Typography>
      <Typography
        variant="body2"
        sx={{
          fontFamily: copyable ? 'monospace' : 'inherit',
          wordBreak: 'break-word',
          cursor: copyable ? 'pointer' : 'default',
          '&:hover': copyable ? { textDecoration: 'underline' } : {},
        }}
        onClick={copyable ? handleCopy : undefined}
        title={copyable ? 'Click to copy' : undefined}
      >
        {value}
      </Typography>
    </Box>
  );
};

interface ImageViewerProps {
  imageUrl: string;
  alt?: string;
  title?: string;
}


export const ImageViewer: React.FC<ImageViewerProps> = ({ imageUrl, alt = 'Image', title }) => {
  const [zoomOpen, setZoomOpen] = React.useState(false);
  const [extractedText, setExtractedText] = React.useState<string | null>(null);
  const [extracting, setExtracting] = React.useState(false);
  const [showText, setShowText] = React.useState(false);

  const extractTextFromImage = async () => {
    setExtracting(true);
    setExtractedText(null);
    try {
      const worker = await createWorker('eng');
      const { data } = await worker.recognize(imageUrl);
      await worker.terminate();
      
      setExtractedText(data.text.trim());
      setShowText(true);
    } catch (error) {
      console.error('OCR Error:', error);
      setExtractedText('Failed to extract text from image');
    } finally {
      setExtracting(false);
    }
  };

  const copyText = () => {
    if (extractedText) {
      navigator.clipboard.writeText(extractedText);
    }
  };

  return (
    <>
      <Box sx={{ mb: 2 }}>
        {title && (
          <Typography variant="caption" color="textSecondary" sx={{ mb: 1, display: 'block' }}>
            {title}
          </Typography>
        )}
        <Paper
          elevation={2}
          sx={{
            p: 1,
            borderRadius: 2,
            overflow: 'hidden',
            cursor: 'pointer',
            position: 'relative',
            '&:hover': {
              boxShadow: 4,
              '& .zoom-icon': {
                opacity: 1,
              },
            },
          }}
          onClick={() => setZoomOpen(true)}
        >
          <Box sx={{ position: 'relative', width: '100%', height: 300 }}>
            <Box
              component="img"
              src={imageUrl}
              alt={alt}
              sx={{
                width: '100%',
                height: 300,
                objectFit: 'contain',
                display: 'block',
                borderRadius: 1,
                bgcolor: 'grey.100',
                userSelect: 'none',
                pointerEvents: 'auto',
                draggable: false,
              }}
              draggable={false}
              onDragStart={(e: React.DragEvent<HTMLImageElement>) => e.preventDefault()}
            />
            
            <Box
              className="zoom-icon"
              sx={{
                position: 'absolute',
                top: '50%',
                left: '50%',
                transform: 'translate(-50%, -50%)',
                bgcolor: 'rgba(0, 0, 0, 0.6)',
                borderRadius: '50%',
                p: 1,
                opacity: 0,
                transition: 'opacity 0.2s',
                pointerEvents: 'none',
              }}
            >
              <ZoomIn sx={{ color: 'white', fontSize: 32 }} />
            </Box>
          </Box>
        </Paper>
        <Box sx={{ display: 'flex', gap: 1, mt: 1, alignItems: 'center', flexWrap: 'wrap' }}>
          <Typography variant="caption" color="textSecondary">
            Click image to zoom
          </Typography>
          <Button
            size="small"
            variant="outlined"
            startIcon={extracting ? <CircularProgress size={16} /> : <TextFields />}
            onClick={extractTextFromImage}
            disabled={extracting}
            sx={{ ml: 'auto' }}
          >
            {extracting ? 'Extracting...' : 'Extract Text'}
          </Button>
        </Box>
        {showText && extractedText && (
          <Paper
            elevation={1}
            sx={{
              mt: 2,
              p: 2,
              bgcolor: 'background.default',
              borderRadius: 1,
              maxHeight: 200,
              overflowY: 'auto',
            }}
          >
            <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 1 }}>
              <Typography variant="caption" fontWeight="bold" color="textSecondary">
                Extracted Text:
              </Typography>
              <IconButton size="small" onClick={copyText} title="Copy text">
                <ContentCopy fontSize="small" />
              </IconButton>
            </Box>
            <Typography
              variant="body2"
              sx={{
                whiteSpace: 'pre-wrap',
                wordBreak: 'break-word',
                userSelect: 'text',
                cursor: 'text',
              }}
            >
              {extractedText || 'No text found in image'}
            </Typography>
          </Paper>
        )}
      </Box>

      {/* Zoom Modal */}
      <Modal
        open={zoomOpen}
        onClose={() => setZoomOpen(false)}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
        sx={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
        }}
      >
        <Fade in={zoomOpen}>
          <Box
            sx={{
              position: 'relative',
              maxWidth: '90vw',
              maxHeight: '90vh',
              outline: 'none',
            }}
            onClick={() => setZoomOpen(false)}
          >
            <IconButton
              onClick={() => setZoomOpen(false)}
              sx={{
                position: 'absolute',
                top: 8,
                right: 8,
                bgcolor: 'rgba(0, 0, 0, 0.6)',
                color: 'white',
                zIndex: 1,
                '&:hover': {
                  bgcolor: 'rgba(0, 0, 0, 0.8)',
                },
              }}
            >
              <Close />
            </IconButton>
            <Box
              component="img"
              src={imageUrl}
              alt={alt}
              sx={{
                maxWidth: '90vw',
                maxHeight: '90vh',
                width: 'auto',
                height: 'auto',
                objectFit: 'contain',
                borderRadius: 1,
                boxShadow: 24,
                userSelect: 'text',
                WebkitUserSelect: 'text',
                MozUserSelect: 'text',
                msUserSelect: 'text',
                pointerEvents: 'auto',
                draggable: false,
              }}
              draggable={false}
              onDragStart={(e: React.DragEvent<HTMLImageElement>) => e.preventDefault()}
            />
          </Box>
        </Fade>
      </Modal>
    </>
  );
};

