import React, { useState, useEffect } from 'react';
import {
  Box,
  Card,
  CardContent,
  Typography,
  Button,
  Chip,
  IconButton,
  CircularProgress,
  Alert,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Grid,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Tabs,
  Tab,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Pagination,
  Tooltip,
} from '@mui/material';
import { ArrowBack, Add, Edit, Delete, Refresh, People } from '@mui/icons-material';
import { useNavigate, useParams } from 'react-router-dom';
import { apiService } from '../../services/api';

interface Package {
  id: number;
  name: string;
  active: boolean;
  created_at: string;
  updated_at: string | null;
}

interface Commission {
  id: number;
  operator_id: number;
  operator_name?: string;
  operator_type?: string;
  amount: string;
  our_amount: string;
  created_at?: string;
  updated_at?: string | null;
}

interface Operator {
  id: number;
  name: string;
  type: string;
  code: string;
}

interface User {
  id: string;
  username: string;
  name: string;
  email: string | null;
  phone: string;
  created_at: string;
}

const PackageDetailsPage: React.FC = () => {
  const navigate = useNavigate();
  const { packageId } = useParams<{ packageId: string }>();
  const [packageData, setPackageData] = useState<Package | null>(null);
  const [commissions, setCommissions] = useState<Commission[]>([]);
  const [operators, setOperators] = useState<Operator[]>([]);
  const [users, setUsers] = useState<User[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [success, setSuccess] = useState<string | null>(null);
  const [activeTab, setActiveTab] = useState(0);
  const [commissionDialog, setCommissionDialog] = useState(false);
  const [usersPage, setUsersPage] = useState(1);
  const [usersSize, setUsersSize] = useState(20);
  const [usersTotalPages, setUsersTotalPages] = useState(1);
  const [usersTotal, setUsersTotal] = useState(0);

  const [commissionForm, setCommissionForm] = useState({
    operator_id: '',
    amount: '',
    our_amount: '',
  });

  useEffect(() => {
    if (packageId) {
      fetchPackageDetails();
      fetchOperators();
    }
  }, [packageId]);

  useEffect(() => {
    if (packageId && activeTab === 1) {
      // Commissions tab - already loaded with package details
    } else if (packageId && activeTab === 2) {
      fetchUsers();
    }
  }, [packageId, activeTab, usersPage, usersSize]);

  const fetchPackageDetails = async () => {
    if (!packageId) return;
    setLoading(true);
    setError(null);
    try {
      const response = await apiService.getPackageById(parseInt(packageId));
      if (response.data) {
        const data = response.data as any;
        setPackageData(data.package || data);
        setCommissions(data.commissions || []);
      }
    } catch (err: any) {
      console.error('Package fetch error:', err);
      setError(err.message || 'Failed to fetch package details');
    } finally {
      setLoading(false);
    }
  };

  const fetchOperators = async () => {
    try {
      // Using app endpoint to get operators
      const response = await fetch(`${import.meta.env.VITE_API_BASE_URL || 'https://api.laddooapp.com'}/app/recharge/operators`);
      const data = await response.json();
      if (data.status === 'success' && data.data) {
        setOperators(data.data.operators || []);
      }
    } catch (err) {
      console.error('Operators fetch error:', err);
    }
  };

  const fetchUsers = async () => {
    if (!packageId) return;
    setLoading(true);
    setError(null);
    try {
      const response = await apiService.getUsersByPackage(parseInt(packageId), {
        page: usersPage,
        size: usersSize,
      });
      if (response.data) {
        const data = response.data as any;
        setUsers(data.items || []);
        setUsersTotalPages(data.pages || 1);
        setUsersTotal(data.total || 0);
      }
    } catch (err: any) {
      console.error('Users fetch error:', err);
      setError(err.message || 'Failed to fetch users');
    } finally {
      setLoading(false);
    }
  };

  const handleAddCommission = () => {
    setCommissionForm({
      operator_id: '',
      amount: '',
      our_amount: '',
    });
    setCommissionDialog(true);
    setError(null);
  };

  const handleEditCommission = (commission: Commission) => {
    setCommissionForm({
      operator_id: String(commission.operator_id),
      amount: commission.amount,
      our_amount: commission.our_amount,
    });
    setCommissionDialog(true);
    setError(null);
  };

  // Get available operators (those without existing commissions)
  // When editing, include the current operator
  const getAvailableOperators = () => {
    const existingOperatorIds = new Set(commissions.map((c: Commission) => c.operator_id));
    const currentOperatorId = commissionForm.operator_id ? parseInt(commissionForm.operator_id) : null;
    
    return operators.filter((op: Operator) => {
      // Include if not in existing commissions, or if it's the one being edited
      return !existingOperatorIds.has(op.id) || op.id === currentOperatorId;
    });
  };

  const isEditingCommission = () => {
    return commissionForm.operator_id && commissions.some((c: Commission) => c.operator_id === parseInt(commissionForm.operator_id));
  };

  const handleSubmitCommission = async () => {
    if (!packageId || !commissionForm.operator_id || !commissionForm.amount || !commissionForm.our_amount) {
      setError('All fields are required');
      return;
    }

    try {
      await apiService.addPackageCommission(parseInt(packageId), {
        operator_id: parseInt(commissionForm.operator_id),
        amount: commissionForm.amount,
        our_amount: commissionForm.our_amount,
      });
      setSuccess('Commission added/updated successfully');
      setCommissionDialog(false);
      setCommissionForm({
        operator_id: '',
        amount: '',
        our_amount: '',
      });
      fetchPackageDetails();
    } catch (err: any) {
      setError(err.message || 'Failed to add commission');
    }
  };

  return (
    <Box>
      <Box sx={{ display: 'flex', alignItems: 'center', gap: 2, mb: 3 }}>
        <IconButton onClick={() => navigate('/packages')}>
          <ArrowBack />
        </IconButton>
        <Typography variant="h4">
          Package Details
        </Typography>
        {packageData && (
          <Chip
            label={packageData.active ? 'Active' : 'Inactive'}
            color={packageData.active ? 'success' : 'default'}
          />
        )}
      </Box>

      {error && (
        <Alert severity="error" sx={{ mb: 2 }} onClose={() => setError(null)}>
          {error}
        </Alert>
      )}

      {success && (
        <Alert severity="success" sx={{ mb: 2 }} onClose={() => setSuccess(null)}>
          {success}
        </Alert>
      )}

      {loading && !packageData ? (
        <Box sx={{ display: 'flex', justifyContent: 'center', p: 4 }}>
          <CircularProgress />
        </Box>
      ) : (
        <>
          {/* Package Info Card */}
          {packageData && (
            <Card sx={{ mb: 3 }}>
              <CardContent>
                <Grid container spacing={2}>
                  <Grid item xs={12} sm={6}>
                    <Typography variant="body2" color="textSecondary">
                      Package Name
                    </Typography>
                    <Typography variant="h6">
                      {packageData.name}
                    </Typography>
                  </Grid>
                  <Grid item xs={12} sm={6}>
                    <Typography variant="body2" color="textSecondary">
                      Package ID
                    </Typography>
                    <Typography variant="h6">
                      #{packageData.id}
                    </Typography>
                  </Grid>
                  <Grid item xs={12} sm={6}>
                    <Typography variant="body2" color="textSecondary">
                      Created At
                    </Typography>
                    <Typography variant="body1">
                      {new Date(packageData.created_at).toLocaleString()}
                    </Typography>
                  </Grid>
                  {packageData.updated_at && (
                    <Grid item xs={12} sm={6}>
                      <Typography variant="body2" color="textSecondary">
                        Updated At
                      </Typography>
                      <Typography variant="body1">
                        {new Date(packageData.updated_at).toLocaleString()}
                      </Typography>
                    </Grid>
                  )}
                </Grid>
              </CardContent>
            </Card>
          )}

          {/* Tabs */}
          <Card>
            <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
              <Tabs value={activeTab} onChange={(_, newValue) => setActiveTab(newValue)}>
                <Tab label="Commissions" />
                <Tab label={`Users (${usersTotal})`} />
              </Tabs>
            </Box>

            <CardContent>
              {/* Commissions Tab */}
              {activeTab === 0 && (
                <Box>
                  <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
                    <Typography variant="h6">
                      Commission Structures ({commissions.length})
                    </Typography>
                    <Button
                      variant="contained"
                      startIcon={<Add />}
                      onClick={handleAddCommission}
                    >
                      Add Commission
                    </Button>
                  </Box>

                  {commissions.length === 0 ? (
                    <Typography color="textSecondary" align="center" sx={{ py: 4 }}>
                      No commissions configured
                    </Typography>
                  ) : (
                    <TableContainer component={Paper} variant="outlined">
                      <Table>
                        <TableHead>
                          <TableRow>
                            <TableCell>Operator</TableCell>
                            <TableCell>Type</TableCell>
                            <TableCell>Commission Amount</TableCell>
                            <TableCell>Our Amount</TableCell>
                            <TableCell align="right">Actions</TableCell>
                          </TableRow>
                        </TableHead>
                        <TableBody>
                          {commissions.map((commission) => (
                            <TableRow key={commission.id} hover>
                              <TableCell>
                                <Typography variant="body2" fontWeight="medium">
                                  {commission.operator_name || `Operator #${commission.operator_id}`}
                                </Typography>
                              </TableCell>
                              <TableCell>
                                {commission.operator_type && (
                                  <Chip label={commission.operator_type} size="small" />
                                )}
                              </TableCell>
                              <TableCell>
                                <Typography variant="body2">
                                  {commission.amount}
                                </Typography>
                              </TableCell>
                              <TableCell>
                                <Typography variant="body2" fontWeight="medium" color="primary">
                                  {commission.our_amount}
                                </Typography>
                              </TableCell>
                              <TableCell align="right">
                                <IconButton
                                  size="small"
                                  color="primary"
                                  onClick={() => handleEditCommission(commission)}
                                  title="Edit Commission"
                                >
                                  <Edit fontSize="small" />
                                </IconButton>
                              </TableCell>
                            </TableRow>
                          ))}
                        </TableBody>
                      </Table>
                    </TableContainer>
                  )}
                </Box>
              )}

              {/* Users Tab */}
              {activeTab === 1 && (
                <Box>
                  <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
                    <Typography variant="h6">
                      Users with this Package ({usersTotal})
                    </Typography>
                    <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
                      <FormControl size="small" sx={{ minWidth: 100 }}>
                        <InputLabel>Per Page</InputLabel>
                        <Select
                          value={usersSize}
                          label="Per Page"
                          onChange={(e) => {
                            setUsersSize(Number(e.target.value));
                            setUsersPage(1);
                          }}
                        >
                          <MenuItem value={10}>10</MenuItem>
                          <MenuItem value={20}>20</MenuItem>
                          <MenuItem value={50}>50</MenuItem>
                          <MenuItem value={100}>100</MenuItem>
                        </Select>
                      </FormControl>
                      <IconButton onClick={fetchUsers} title="Refresh">
                        <Refresh />
                      </IconButton>
                    </Box>
                  </Box>

                  {users.length === 0 ? (
                    <Typography color="textSecondary" align="center" sx={{ py: 4 }}>
                      No users found
                    </Typography>
                  ) : (
                    <>
                      <TableContainer component={Paper} variant="outlined">
                        <Table>
                          <TableHead>
                            <TableRow>
                              <TableCell>Name</TableCell>
                              <TableCell>Username</TableCell>
                              <TableCell>Email</TableCell>
                              <TableCell>Phone</TableCell>
                              <TableCell>Joined</TableCell>
                            </TableRow>
                          </TableHead>
                          <TableBody>
                            {users.map((user) => (
                              <TableRow key={user.id} hover>
                                <TableCell>
                                  <Typography variant="body2" fontWeight="medium">
                                    {user.name || 'N/A'}
                                  </Typography>
                                </TableCell>
                                <TableCell>{user.username}</TableCell>
                                <TableCell>{user.email || 'N/A'}</TableCell>
                                <TableCell>{user.phone}</TableCell>
                                <TableCell>
                                  <Typography variant="caption" color="textSecondary">
                                    {new Date(user.created_at).toLocaleDateString()}
                                  </Typography>
                                </TableCell>
                              </TableRow>
                            ))}
                          </TableBody>
                        </Table>
                      </TableContainer>

                      {usersTotalPages > 1 && (
                        <Box sx={{ display: 'flex', justifyContent: 'center', mt: 3 }}>
                          <Pagination
                            count={usersTotalPages}
                            page={usersPage}
                            onChange={(_, value) => setUsersPage(value)}
                            color="primary"
                            showFirstButton
                            showLastButton
                          />
                        </Box>
                      )}
                    </>
                  )}
                </Box>
              )}
            </CardContent>
          </Card>
        </>
      )}

      {/* Add/Edit Commission Dialog */}
      <Dialog 
        open={commissionDialog} 
        onClose={() => {
          setCommissionDialog(false);
          setCommissionForm({
            operator_id: '',
            amount: '',
            our_amount: '',
          });
        }} 
        maxWidth="sm" 
        fullWidth
      >
        <DialogTitle>{isEditingCommission() ? 'Edit Commission' : 'Add Commission'}</DialogTitle>
        <DialogContent>
          <Box sx={{ pt: 2 }}>
            <FormControl fullWidth sx={{ mb: 2 }}>
              <InputLabel>Operator</InputLabel>
              <Select
                value={commissionForm.operator_id}
                label="Operator"
                onChange={(e) => setCommissionForm({ ...commissionForm, operator_id: e.target.value })}
                disabled={isEditingCommission()}
              >
                {getAvailableOperators().length === 0 ? (
                  <MenuItem disabled>No available operators (all have commissions)</MenuItem>
                ) : (
                  getAvailableOperators().map((op) => (
                    <MenuItem key={op.id} value={String(op.id)}>
                      {op.name} ({op.type})
                    </MenuItem>
                  ))
                )}
              </Select>
            </FormControl>
            <TextField
              label="Commission Amount"
              variant="outlined"
              fullWidth
              value={commissionForm.amount}
              onChange={(e) => setCommissionForm({ ...commissionForm, amount: e.target.value })}
              sx={{ mb: 2 }}
              placeholder="e.g., 2.5% or 5.00"
              required
            />
            <TextField
              label="Our Amount"
              variant="outlined"
              fullWidth
              value={commissionForm.our_amount}
              onChange={(e) => setCommissionForm({ ...commissionForm, our_amount: e.target.value })}
              placeholder="e.g., 1.5% or 3.00"
              required
            />
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setCommissionDialog(false)}>Cancel</Button>
          <Button onClick={handleSubmitCommission} variant="contained">
            Add/Update
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default PackageDetailsPage;
