import React, { useState, useEffect } from 'react';
import {
  Box,
  Card,
  CardContent,
  Typography,
  Button,
  Chip,
  IconButton,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  CircularProgress,
  Alert,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Grid,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Switch,
  FormControlLabel,
  Tooltip,
} from '@mui/material';
import { Add, Edit, Delete, Refresh, Visibility, People } from '@mui/icons-material';
import { useNavigate } from 'react-router-dom';
import { apiService } from '../../services/api';

interface Package {
  id: number;
  name: string;
  active: boolean;
  commission_count?: number;
  created_at?: string;
  updated_at?: string | null;
}

const PackagesPage: React.FC = () => {
  const navigate = useNavigate();
  const [packages, setPackages] = useState<Package[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [success, setSuccess] = useState<string | null>(null);
  const [activeFilter, setActiveFilter] = useState<boolean | null>(null);
  const [dialogOpen, setDialogOpen] = useState(false);
  const [deleteDialog, setDeleteDialog] = useState<{ open: boolean; packageId: number | null }>({
    open: false,
    packageId: null,
  });
  const [editingPackage, setEditingPackage] = useState<Package | null>(null);

  const [formData, setFormData] = useState({
    name: '',
    active: true,
  });

  const fetchPackages = async () => {
    setLoading(true);
    setError(null);
    try {
      const response = await apiService.getPackages(activeFilter !== null ? activeFilter : undefined);
      
      if (response.data) {
        const data = response.data as any;
        setPackages(data.packages || []);
      }
    } catch (err: any) {
      console.error('Packages fetch error:', err);
      setError(err.message || 'Failed to fetch packages');
      setPackages([]);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchPackages();
  }, [activeFilter]);

  const handleOpenDialog = (pkg?: Package) => {
    if (pkg) {
      setEditingPackage(pkg);
      setFormData({
        name: pkg.name,
        active: pkg.active,
      });
    } else {
      setEditingPackage(null);
      setFormData({
        name: '',
        active: true,
      });
    }
    setDialogOpen(true);
    setError(null);
    setSuccess(null);
  };

  const handleCloseDialog = () => {
    setDialogOpen(false);
    setEditingPackage(null);
    setFormData({
      name: '',
      active: true,
    });
  };

  const handleSubmit = async () => {
    if (!formData.name.trim()) {
      setError('Package name is required');
      return;
    }

    try {
      if (editingPackage) {
        await apiService.updatePackage(editingPackage.id, formData);
        setSuccess('Package updated successfully');
      } else {
        await apiService.createPackage(formData);
        setSuccess('Package created successfully');
      }
      handleCloseDialog();
      fetchPackages();
    } catch (err: any) {
      setError(err.message || `Failed to ${editingPackage ? 'update' : 'create'} package`);
    }
  };

  const handleDeleteClick = (pkg: Package) => {
    setDeleteDialog({ open: true, packageId: pkg.id });
    setError(null);
  };

  const handleDeleteConfirm = async () => {
    if (!deleteDialog.packageId) return;

    try {
      await apiService.deletePackage(deleteDialog.packageId);
      setSuccess('Package deleted/deactivated successfully');
      setDeleteDialog({ open: false, packageId: null });
      fetchPackages();
    } catch (err: any) {
      setError(err.message || 'Failed to delete package');
      setDeleteDialog({ open: false, packageId: null });
    }
  };

  const handleViewDetails = (packageId: number) => {
    navigate(`/packages/${packageId}`);
  };

  return (
    <Box>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
        <Typography variant="h4">
          Package Management
        </Typography>
        <Button
          variant="contained"
          startIcon={<Add />}
          onClick={() => handleOpenDialog()}
        >
          Create Package
        </Button>
      </Box>

      {error && (
        <Alert severity="error" sx={{ mb: 2 }} onClose={() => setError(null)}>
          {error}
        </Alert>
      )}

      {success && (
        <Alert severity="success" sx={{ mb: 2 }} onClose={() => setSuccess(null)}>
          {success}
        </Alert>
      )}

      {/* Filters */}
      <Card sx={{ mb: 3 }}>
        <CardContent>
          <Grid container spacing={2} alignItems="center">
            <Grid item xs={12} sm={6} md={3}>
              <FormControl fullWidth size="small">
                <InputLabel>Status</InputLabel>
                <Select
                  value={activeFilter === null ? '' : activeFilter}
                  label="Status"
                  onChange={(e) => setActiveFilter(e.target.value === '' ? null : e.target.value)}
                >
                  <MenuItem value="">All Packages</MenuItem>
                  <MenuItem value={true}>Active</MenuItem>
                  <MenuItem value={false}>Inactive</MenuItem>
                </Select>
              </FormControl>
            </Grid>
            <Grid item xs={12} sm={6} md={3}>
              <Button
                variant="outlined"
                startIcon={<Refresh />}
                onClick={fetchPackages}
                fullWidth
              >
                Refresh
              </Button>
            </Grid>
          </Grid>
        </CardContent>
      </Card>

      {/* Packages Table */}
      {loading ? (
        <Box sx={{ display: 'flex', justifyContent: 'center', p: 4 }}>
          <CircularProgress />
        </Box>
      ) : packages.length === 0 ? (
        <Card>
          <CardContent>
            <Typography color="textSecondary" align="center" sx={{ py: 4 }}>
              No packages found
            </Typography>
          </CardContent>
        </Card>
      ) : (
        <TableContainer component={Paper} variant="outlined">
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>ID</TableCell>
                <TableCell>Name</TableCell>
                <TableCell>Status</TableCell>
                <TableCell>Commissions</TableCell>
                <TableCell>Created At</TableCell>
                <TableCell align="right">Actions</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {packages.map((pkg) => (
                <TableRow key={pkg.id} hover>
                  <TableCell>{pkg.id}</TableCell>
                  <TableCell>
                    <Typography variant="body2" fontWeight="medium">
                      {pkg.name}
                    </Typography>
                  </TableCell>
                  <TableCell>
                    <Chip
                      label={pkg.active ? 'Active' : 'Inactive'}
                      color={pkg.active ? 'success' : 'default'}
                      size="small"
                    />
                  </TableCell>
                  <TableCell>
                    <Chip
                      label={pkg.commission_count || 0}
                      size="small"
                      variant="outlined"
                    />
                  </TableCell>
                  <TableCell>
                    {pkg.created_at ? (
                      <Typography variant="caption" color="textSecondary">
                        {new Date(pkg.created_at).toLocaleDateString()}
                      </Typography>
                    ) : (
                      <Typography variant="caption" color="textSecondary">
                        N/A
                      </Typography>
                    )}
                  </TableCell>
                  <TableCell align="right">
                    <Box sx={{ display: 'flex', gap: 1, justifyContent: 'flex-end' }}>
                      <Tooltip title="View Details">
                        <IconButton
                          size="small"
                          color="primary"
                          onClick={() => handleViewDetails(pkg.id)}
                        >
                          <Visibility fontSize="small" />
                        </IconButton>
                      </Tooltip>
                      <Tooltip title="Edit">
                        <IconButton
                          size="small"
                          color="primary"
                          onClick={() => handleOpenDialog(pkg)}
                        >
                          <Edit fontSize="small" />
                        </IconButton>
                      </Tooltip>
                      <Tooltip title="Delete">
                        <IconButton
                          size="small"
                          color="error"
                          onClick={() => handleDeleteClick(pkg)}
                        >
                          <Delete fontSize="small" />
                        </IconButton>
                      </Tooltip>
                    </Box>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      )}

      {/* Create/Edit Dialog */}
      <Dialog open={dialogOpen} onClose={handleCloseDialog} maxWidth="sm" fullWidth>
        <DialogTitle>
          {editingPackage ? 'Edit Package' : 'Create Package'}
        </DialogTitle>
        <DialogContent>
          <Box sx={{ pt: 2 }}>
            <TextField
              label="Package Name"
              variant="outlined"
              fullWidth
              value={formData.name}
              onChange={(e) => setFormData({ ...formData, name: e.target.value })}
              sx={{ mb: 2 }}
              required
            />
            <FormControlLabel
              control={
                <Switch
                  checked={formData.active}
                  onChange={(e) => setFormData({ ...formData, active: e.target.checked })}
                />
              }
              label="Active"
            />
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseDialog}>Cancel</Button>
          <Button onClick={handleSubmit} variant="contained">
            {editingPackage ? 'Update' : 'Create'}
          </Button>
        </DialogActions>
      </Dialog>

      {/* Delete Confirmation Dialog */}
      <Dialog open={deleteDialog.open} onClose={() => setDeleteDialog({ open: false, packageId: null })}>
        <DialogTitle>Delete Package</DialogTitle>
        <DialogContent>
          <Typography>
            Are you sure you want to delete this package? If users are using this package, it will be deactivated instead of deleted.
          </Typography>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setDeleteDialog({ open: false, packageId: null })}>Cancel</Button>
          <Button onClick={handleDeleteConfirm} color="error" variant="contained">
            Delete
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default PackagesPage;
