import React, { useState, useEffect } from 'react';
import {
  Box,
  Card,
  CardContent,
  Typography,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Button,
  IconButton,
  Pagination,
  CircularProgress,
  Alert,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
} from '@mui/material';
import { Add, Edit, Delete, Refresh } from '@mui/icons-material';
import { apiService } from '../../services/api';

interface AdSlot {
  id: number;
  slot_code: string;
  title: string;
  created_at: string;
}

const AdsSlotsPage: React.FC = () => {
  const [slots, setSlots] = useState<AdSlot[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [success, setSuccess] = useState<string | null>(null);
  const [page, setPage] = useState(1);
  const [size] = useState(20);
  const [totalPages, setTotalPages] = useState(1);
  const [dialogOpen, setDialogOpen] = useState(false);
  const [editingSlot, setEditingSlot] = useState<AdSlot | null>(null);
  const [deleteDialog, setDeleteDialog] = useState<{ open: boolean; slotId: number | null }>({
    open: false,
    slotId: null,
  });

  const [formData, setFormData] = useState({
    slot_code: '',
    title: '',
  });

  const fetchSlots = async () => {
    setLoading(true);
    setError(null);
    try {
      const response = await apiService.getAdSlots({ page, size });
      
      if (response.data) {
        const data = response.data as any;
        setSlots(data.items || data.data || []);
        setTotalPages(data.total_pages || 1);
      }
    } catch (err: any) {
      setError(err.message || 'Failed to fetch ad slots');
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchSlots();
  }, [page]);

  const handleOpenDialog = (slot?: AdSlot) => {
    if (slot) {
      setEditingSlot(slot);
      setFormData({
        slot_code: slot.slot_code,
        title: slot.title,
      });
    } else {
      setEditingSlot(null);
      setFormData({
        slot_code: '',
        title: '',
      });
    }
    setDialogOpen(true);
  };

  const handleCloseDialog = () => {
    setDialogOpen(false);
    setEditingSlot(null);
  };

  const handleSubmit = async () => {
    try {
      if (editingSlot) {
        await apiService.updateAdSlot(editingSlot.id, formData.title);
        setSuccess('Ad slot updated successfully');
      } else {
        await apiService.createAdSlot(formData.slot_code, formData.title);
        setSuccess('Ad slot created successfully');
      }
      
      handleCloseDialog();
      fetchSlots();
    } catch (err: any) {
      setError(err.message || 'Failed to save ad slot');
    }
  };

  const handleDelete = async () => {
    if (!deleteDialog.slotId) return;
    
    try {
      await apiService.deleteAdSlot(deleteDialog.slotId);
      setSuccess('Ad slot deleted successfully');
      setDeleteDialog({ open: false, slotId: null });
      fetchSlots();
    } catch (err: any) {
      setError(err.message || 'Failed to delete ad slot');
    }
  };

  return (
    <Box>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
        <Typography variant="h4">Ad Slots Management</Typography>
        <Button
          variant="contained"
          startIcon={<Add />}
          onClick={() => handleOpenDialog()}
        >
          Create Slot
        </Button>
      </Box>

      <Card sx={{ mb: 3 }}>
        <CardContent>
          <Box sx={{ display: 'flex', gap: 2, alignItems: 'center' }}>
            <IconButton onClick={fetchSlots} color="primary">
              <Refresh />
            </IconButton>
          </Box>
        </CardContent>
      </Card>

      {error && (
        <Alert severity="error" sx={{ mb: 2 }} onClose={() => setError(null)}>
          {error}
        </Alert>
      )}

      {success && (
        <Alert severity="success" sx={{ mb: 2 }} onClose={() => setSuccess(null)}>
          {success}
        </Alert>
      )}

      <Card>
        <CardContent>
          {loading ? (
            <Box sx={{ display: 'flex', justifyContent: 'center', p: 4 }}>
              <CircularProgress />
            </Box>
          ) : (
            <>
              <TableContainer component={Paper} variant="outlined">
                <Table>
                  <TableHead>
                    <TableRow>
                      <TableCell>ID</TableCell>
                      <TableCell>Slot Code</TableCell>
                      <TableCell>Title</TableCell>
                      <TableCell>Created At</TableCell>
                      <TableCell>Actions</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {slots.length === 0 ? (
                      <TableRow>
                        <TableCell colSpan={5} align="center">
                          <Typography color="textSecondary">No ad slots found</Typography>
                        </TableCell>
                      </TableRow>
                    ) : (
                      slots.map((slot) => (
                        <TableRow key={slot.id} hover>
                          <TableCell>{slot.id}</TableCell>
                          <TableCell>{slot.slot_code}</TableCell>
                          <TableCell>{slot.title}</TableCell>
                          <TableCell>
                            {new Date(slot.created_at).toLocaleString()}
                          </TableCell>
                          <TableCell>
                            <IconButton
                              size="small"
                              color="primary"
                              onClick={() => handleOpenDialog(slot)}
                            >
                              <Edit />
                            </IconButton>
                            <IconButton
                              size="small"
                              color="error"
                              onClick={() => setDeleteDialog({ open: true, slotId: slot.id })}
                            >
                              <Delete />
                            </IconButton>
                          </TableCell>
                        </TableRow>
                      ))
                    )}
                  </TableBody>
                </Table>
              </TableContainer>

              {totalPages > 1 && (
                <Box sx={{ display: 'flex', justifyContent: 'center', mt: 3 }}>
                  <Pagination
                    count={totalPages}
                    page={page}
                    onChange={(_, newPage) => setPage(newPage)}
                    color="primary"
                  />
                </Box>
              )}
            </>
          )}
        </CardContent>
      </Card>

      <Dialog open={dialogOpen} onClose={handleCloseDialog} maxWidth="sm" fullWidth>
        <DialogTitle>{editingSlot ? 'Edit Ad Slot' : 'Create Ad Slot'}</DialogTitle>
        <DialogContent>
          <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2, mt: 1 }}>
            <TextField
              label="Slot Code"
              fullWidth
              required
              disabled={!!editingSlot}
              value={formData.slot_code}
              onChange={(e) => setFormData({ ...formData, slot_code: e.target.value })}
            />
            <TextField
              label="Title"
              fullWidth
              required
              value={formData.title}
              onChange={(e) => setFormData({ ...formData, title: e.target.value })}
            />
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseDialog}>Cancel</Button>
          <Button onClick={handleSubmit} variant="contained">
            {editingSlot ? 'Update' : 'Create'}
          </Button>
        </DialogActions>
      </Dialog>

      <Dialog open={deleteDialog.open} onClose={() => setDeleteDialog({ open: false, slotId: null })}>
        <DialogTitle>Delete Ad Slot</DialogTitle>
        <DialogContent>
          <Typography>Are you sure you want to delete this ad slot? This will also delete all associated ad contents.</Typography>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setDeleteDialog({ open: false, slotId: null })}>Cancel</Button>
          <Button onClick={handleDelete} variant="contained" color="error">
            Delete
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default AdsSlotsPage;

