import React, { createContext, useContext, useEffect, useState } from "react";
import axios from "axios";

interface Admin {
  id: string;
  email: string;
  name?: string | null;
}

interface AuthContextValue {
  admin: Admin | null;
  accessToken: string | null;
  loading: boolean;
  login: (email: string, password: string) => Promise<void>;
  logout: () => void;
}

const AuthContext = createContext<AuthContextValue | undefined>(undefined);

const ACCESS_TOKEN_KEY = "laddoo_admin_access_token";

const api = axios.create({
  baseURL: import.meta.env.VITE_API_BASE_URL ?? "https://api.laddooapp.com",
});

api.interceptors.request.use((config) => {
  const token = localStorage.getItem(ACCESS_TOKEN_KEY);
  if (token) {
    config.headers = {
      ...config.headers,
      Authorization: `Bearer ${token}`,
    };
  }
  return config;
});

export const AuthProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const [admin, setAdmin] = useState<Admin | null>(null);
  const [accessToken, setAccessToken] = useState<string | null>(
    () => localStorage.getItem(ACCESS_TOKEN_KEY)
  );
  const [loading, setLoading] = useState<boolean>(true);

  useEffect(() => {
    const init = async () => {
      if (!accessToken) {
        setLoading(false);
        return;
      }
      try {
        const res = await api.get("/admin/auth/me");
        const data = res.data?.data ?? {};
        setAdmin({
          id: String(data.id ?? ""),
          email: data.email ?? "",
          name: data.name ?? data.full_name ?? null,
        });
      } catch {
        localStorage.removeItem(ACCESS_TOKEN_KEY);
        setAccessToken(null);
        setAdmin(null);
      } finally {
        setLoading(false);
      }
    };
    void init();
  }, [accessToken]);

  const login = async (email: string, password: string) => {
    const res = await api.post("/admin/auth/login", { email, password });
    const token = res.data?.data?.access_token ?? res.data?.data?.token;
    if (!token) {
      throw new Error("Token not found in response");
    }
    localStorage.setItem(ACCESS_TOKEN_KEY, token);
    setAccessToken(token);
    const meRes = await api.get("/admin/auth/me");
    const data = meRes.data?.data ?? {};
    setAdmin({
      id: String(data.id ?? ""),
      email: data.email ?? "",
      name: data.name ?? data.full_name ?? null,
    });
  };

  const logout = () => {
    localStorage.removeItem(ACCESS_TOKEN_KEY);
    setAccessToken(null);
    setAdmin(null);
  };

  return (
    <AuthContext.Provider value={{ admin, accessToken, loading, login, logout }}>
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = (): AuthContextValue => {
  const ctx = useContext(AuthContext);
  if (!ctx) {
    throw new Error("useAuth must be used within AuthProvider");
  }
  return ctx;
};

export { api as authApi };


