import { Navigate, Route, Routes } from "react-router-dom";
import { AuthProvider } from "./auth/AuthContext";
import { ProtectedRoute } from "./auth/ProtectedRoute";
import { LoginPage } from "./pages/auth/LoginPage";
import { DashboardLayout } from "./layout/DashboardLayout";
import { OverviewPage } from "./pages/overview/OverviewPage";
import UsersPage from "./pages/users/UsersPage";
import WalletsPage from "./pages/wallets/WalletsPage";
import WalletTransactionsPage from "./pages/transactions/WalletTransactionsPage";
import RechargeTransactionsPage from "./pages/transactions/RechargeTransactionsPage";
import FundRequestsPage from "./pages/fund-requests/FundRequestsPage";
import OffersPage from "./pages/offers/OffersPage";
import AdsSlotsPage from "./pages/ads/AdsSlotsPage";
import AdsContentsPage from "./pages/ads/AdsContentsPage";
import ReferralConfigPage from "./pages/referral/ReferralConfigPage";
import FcmManagementPage from "./pages/fcm/FcmManagementPage";
import NotificationsPage from "./pages/notifications/NotificationsPage";
import WhatsAppMessagesPage from "./pages/whatsapp/WhatsAppMessagesPage";
import LocationUsersPage from "./pages/location/LocationUsersPage";
import PackagesPage from "./pages/packages/PackagesPage";
import PackageDetailsPage from "./pages/packages/PackageDetailsPage";
import SupportChatsPage from "./pages/support/SupportChatsPage";
import ChangePasswordPage from "./pages/settings/ChangePasswordPage";

export default function App() {
  return (
    <AuthProvider>
      <Routes>
        <Route path="/login" element={<LoginPage />} />
        <Route
          path="/"
          element={
            <ProtectedRoute>
              <DashboardLayout />
            </ProtectedRoute>
          }
        >
          <Route index element={<Navigate to="/overview" replace />} />
          <Route path="overview" element={<OverviewPage />} />
          <Route path="users" element={<UsersPage />} />
          <Route path="wallets" element={<WalletsPage />} />
          <Route path="wallet-transactions" element={<WalletTransactionsPage />} />
          <Route path="recharge-transactions" element={<RechargeTransactionsPage />} />
          <Route path="fund-requests" element={<FundRequestsPage />} />
          <Route path="offers" element={<OffersPage />} />
          <Route path="ads/slots" element={<AdsSlotsPage />} />
          <Route path="ads/contents" element={<AdsContentsPage />} />
          <Route path="referral-config" element={<ReferralConfigPage />} />
          <Route path="fcm" element={<FcmManagementPage />} />
          <Route path="notifications" element={<NotificationsPage />} />
          <Route path="whatsapp" element={<WhatsAppMessagesPage />} />
          <Route path="location/users" element={<LocationUsersPage />} />
          <Route path="packages" element={<PackagesPage />} />
          <Route path="packages/:packageId" element={<PackageDetailsPage />} />
          <Route path="support/chats" element={<SupportChatsPage />} />
          <Route path="settings" element={<ChangePasswordPage />} />
        </Route>
        <Route path="*" element={<Navigate to="/" replace />} />
      </Routes>
    </AuthProvider>
  );
}


